import { Button, Container, Typography } from "@material-ui/core";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import { useEffect } from "react";
import "aos/dist/aos.css";
import AOS from "aos";
import { Link } from "react-router-dom";
import ArrowRightAltIcon from "@material-ui/icons/ArrowRightAlt";

const useStyles = makeStyles((theme) => ({
  typo: {
    margin: "0px 0px 0.35em",
    fontFamily: "Inter, sans-serif",
    fontSize: "1rem",
    lineHeight: "1.5",
    textAlign: "center",
    color: "rgb(100, 110, 115)",
    textTransform: "uppercase",
    fontWeight: "500",
  },
  typo2: {
    margin: "0px 0px 0.35em",
    fontFamily: "Montserrat, sans-serif",
    fontSize: "2rem",
    lineHeight: "1.167",
    color:"rgb(45, 55, 72)",
    textAlign: "center",
    fontWeight: "700",
    [theme.breakpoints.up("md")]: {
      fontSize: "2.7849rem",
    },
  },
  typo3: {
    margin: "0px",
    fontFamily: "Inter, sans-serif",
    fontWeight: "500",
    fontSize: "1.125rem",
    lineHeight: "1.6",
    textAlign: "center",
    color: "rgb(100, 110, 115)",
    paddingBottom: theme.spacing(3),
  },

  container: {
    display: "flex",
    alignItems: "center",
    minHeight: "50vh",
    flexDirection: "column",
    justifyContent: "center",
    paddingBottom: "2rem",
  },
  rocket: {
    maxWidth: "460px",
    width: "100%",
  },
}));

const ColorButton = withStyles((theme) => ({
  root: {
    display: "inline-flex",
    WebkitBoxAlign: "center",
    alignItems: "center",
    WebkitBoxPack: "center",
    justifyContent: "center",
    position: "relative",
    boxSizing: "border-box",
    WebkitTapHighlightColor: "transparent",
    outline: "0px",
    border: "0px",
    margin: "0px",
    cursor: "pointer",
    userSelect: "none",
    verticalAlign: "middle",
    appearance: "none",
    textDecoration: "none",
    textTransform: "none",
    fontFamily: "Inter, sans-serif",
    fontSize: "0.9375rem",
    lineHeight: "1.75",
    minWidth: "64px",
    padding: "8px 22px",
    borderRadius: "4px",
    color: "rgb(255, 255, 255)",
    backgroundColor: theme.palette.error.main,
    boxShadow: "rgb(140 152 164 / 10%) 0px 12px 15px",
    "&:hover": {
      textDecoration: "none",
      backgroundColor: theme.palette.error.dark,
      boxShadow: "rgb(140 152 164 / 18%) 0px 10px 40px 10px",
    },
  },
}))(Button);

export const Getintouch = () => {
  const classes = useStyles();
  useEffect(() => {
    AOS.init({ once: false });
  }, []);
  return (
    <Container maxWidth="sm" className={classes.container}>
      <Typography variant="body1" className={classes.typo} gutterBottom >
        CONTACT US
      </Typography>
      <Typography variant="h3" className={classes.typo2} gutterBottom >
        Get in touch
      </Typography>
      <Typography variant="h6" className={classes.typo3} paragraph gutterBottom>
        We'd love to talk about how we can help you.
      </Typography>
      <Link to="/Contact">
      <ColorButton endIcon={<ArrowRightAltIcon />}>Contact us</ColorButton>
      </Link>
    </Container>
  );
};
