import TwitterIcon from "@material-ui/icons/Twitter";
import FacebookIcon from "@material-ui/icons/Facebook";
import InstagramIcon from "@material-ui/icons/Instagram";
import LinkedInIcon from "@material-ui/icons/LinkedIn";
import IconButton from "@material-ui/core/IconButton";
import { useStyles } from "./contactStyle";
import { Grid } from "@material-ui/core";

export const Social = () => {
  const classes = useStyles();
  const socialinfo = [
    {
      icon: <LinkedInIcon className={classes.icon2} />,
    },
    {
      icon: <FacebookIcon className={classes.icon2} />,
    },
    {
      icon: <TwitterIcon className={classes.icon2} />,
    },
    {
      icon: <InstagramIcon className={classes.icon2} />,
    },
  ];

  return (
    <Grid
      container
      className={classes.consocial}
      align="center"
      justify="center"
    >
      {socialinfo.map((elem, index) => (
        <Grid xs={3} item className={classes.item} key={index}>
          <IconButton  className={classes.iconb}>{elem.icon}</IconButton>
        </Grid>
      ))}
    </Grid>
  );
};
