import React from "react";
// import fr from "../imgs/fr.svg";
import en from "../../imgs/en.svg";
import Drawer from "@material-ui/core/Drawer";
import List from "@material-ui/core/List";
import Divider from "@material-ui/core/Divider";
import IconButton from "@material-ui/core/IconButton";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import HomeIcon from "@material-ui/icons/Home";
import AssignmentIndIcon from "@material-ui/icons/AssignmentInd";
import AccountCircleIcon from "@material-ui/icons/AccountCircle";
export default function Drawe(props) {
  const { classes, handleDrawerClose, theme, open } = props;
  return (
    <Drawer
      className={classes.drawer}
      anchor="right"
      open={open}
      classes={{
        paper: classes.drawerPaper,
      }}
      onClose={handleDrawerClose}
    >
      <div
        onClick={handleDrawerClose}
        onKeyDown={handleDrawerClose}
      >
        <div className={classes.drawerHeader}>
          <IconButton onClick={handleDrawerClose}>
            {theme.direction === "rtl" ? (
              <ChevronLeftIcon />
            ) : (
              <ChevronRightIcon />
            )}
          </IconButton>
        </div>
        <Divider />
        <List>
          <ListItem
            button
            color="primary"
            onClick={() => {
              props.history.push(
                `${props.location.pathname === "/" ? "Apply" : "/"}`
              );
            }}
          >
            <ListItemIcon>
              {props.location.pathname === "/" ? (
                <AssignmentIndIcon color="primary" />
              ) : (
                <HomeIcon color="primary" />
              )}
            </ListItemIcon>
            <ListItemText
              primary={props.location.pathname === "/" ? "Apply" : "Home"}
            />
          </ListItem>
          <ListItem
            button
            color="primary"
            onClick={() => {
              props.history.push("/signin");
            }}
          >
            <ListItemIcon>
              <AccountCircleIcon color="primary" />
            </ListItemIcon>
            <ListItemText primary="Login" />
          </ListItem>
          <Divider />
          <ListItem button color="primary">
            <ListItemIcon>
              <img src={en} className="lang" alt="img" />
            </ListItemIcon>
            <ListItemText primary="Langue" />
          </ListItem>
        </List>
      </div>
    </Drawer>
  );
}
