import { useStyles } from "./contactStyle";
import { Grid } from "@material-ui/core";
import CallIcon from "@material-ui/icons/Call";
import LocationOnIcon from "@material-ui/icons/LocationOn";
import EmailIcon from "@material-ui/icons/Email";
import { Typography } from "@material-ui/core";

export const Contactinfo = () => {
  const classes = useStyles();

  const infos = [
    {
      icon: <CallIcon className={classes.icon} />,
      typo: "+212 6000 0000",
    },
    {
      icon: <EmailIcon className={classes.icon} />,
      typo: "Startincub@um6p.ma",
    },
    {
      icon: <LocationOnIcon className={classes.icon} />,
      typo: " Mohammed VI Polytechnic University",
    },
  ];
  return infos.map((elem, index) => (
    <Grid key={index} container className={classes.conitem} align="left"  alignItems="center" justify="center">
      <Grid xs={4} item className={classes.item}>
        {elem.icon}
      </Grid>
      <Grid xs={8} item className={classes.item}>
        <Typography className={classes.typo3}>{elem.typo}</Typography>
      </Grid>
    </Grid>
  ));
};
