import { React, useEffect } from "react";
import Typography from "@material-ui/core/Typography";
import { Grid } from "@material-ui/core";
import logo from "../imgs/Footerlogo.svg";
import Copyright from "./copyright";
import AOS from "aos";
import "aos/dist/aos.css";
import { useStyles } from "../styling/footerstyle";

function Footer() {
  useEffect(() => {
    AOS.init();
  }, []);

  const elem = [
    {
      name: "Contact Us",
      desc: "Mohammed VI Polytechnic University Lot 660, Hay Moulay Rachid Ben Guerir, 43150, Morocco",
    },
    {
      name: "NAVIGUER",
      desc: "Mohammed VI Polytechnic University Lot 660, Hay Moulay Rachid Ben Guerir, 43150, Morocco",
    },
    {
      name: "CONFIDENTIALITÉ",
      desc: "Mohammed VI Polytechnic University Lot 660, Hay Moulay Rachid Ben Guerir, 43150, Morocco",
    },
    {
      name: "FOLLOW US",
      desc: "Mohammed VI Polytechnic University Lot 660, Hay Moulay Rachid Ben Guerir, 43150, Morocco",
    },
  ];

  const classes = useStyles();
  return (
    <>
      <footer className={classes.footer}>
        <Grid
          container
          alignItems="center"
          justifyContent="center"
          spacing={3}
          className={classes.main}
        >
          <Grid item sm={3}>
            <div className={classes.conlogo}>
              <img
                src={logo}
                className={classes.logo}
                data-aos="zoom-in"
                data-aos-once="false"
                alt="img"
              />
            </div>
          </Grid>
          <Grid
            container
            item
            md={9}
            alignItems="center"
            justifyContent="center"
            spacing={5}
          >
            {elem.map((elem, index) => (
              <Grid item sm={3} key={index}>
                <div
                  className={classes.footerContent}
                  data-aos="fade-up"
                  data-aos-delay={(index * 100).toString()}
                  data-aos-once="false"
                >
                  <Typography
                    variant="h5"
                    color="error"
                    className={classes.formhead}
                  >
                    {elem.name}
                  </Typography>
                  <p>{elem.desc}</p>
                </div>
              </Grid>
            ))}
          </Grid>
        </Grid>
        <Copyright />
      </footer>
    </>
  );
}

export default Footer;
