import { makeStyles } from "@material-ui/core/styles";

import bg from "../imgs/homebg.jpg";
import over from "../imgs/over.svg";
const bgcolor = "rgb(247, 249, 252)";
const useStyles = makeStyles((theme) => ({
  container: {
    position: "relative",
    width: "100%",
    height: "100%",
    backgroundColor: bgcolor,
  },
  formhead: {
    fontWeight: "600",
    // opacity: 0.7,
    fontFamily: "Montserrat, sans-serif",
    textTransform: "uppercase",
    paddingBottom: theme.spacing(3),
    color: theme.palette.primary.main,
    fontSize: ".8rem",
    [theme.breakpoints.up("sm")]: {
      fontSize: "1.25rem",
    }
    // paddingLeft: theme.spacing(1),
  },
  over: {
    background: `url(${over})`,
    width: "100%",
    height: "110vh",
    position: "absolute",
    backgroundPosition: "center",
    backgroundSize: "cover",
    backgroundAttachment: "fixed",
  },
  home: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    width: "100%",
    height: "110vh",
    background: `url(${bg})`,
    backgroundPosition: "center",
    backgroundSize: "cover",
    backgroundAttachment: "fixed",
    overflow: "hidden",
    flexDirection: "column",
    "& *": {
      zIndex: 9,
    },
    // position: "absolute"
  },
  notfound: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    width: "100%",
    height: "100vh",
    paddingTop: "80px",
    backgroundPosition: "center",
    backgroundSize: "cover",
    backgroundAttachment: "fixed",
    overflow: "hidden",
    flexDirection: "column",
    // position: "absolute"
  },
  typo: {
    // marginRight: theme.spacing(5),
    // marginLeft: theme.spacing(5),
    textTransform: "uppercase",
    fontFamily: "'Odibee Sans', cursive",
    fontSize: "2em",
    [theme.breakpoints.up("sm")]: {
      fontSize: "3rem",
    },
    letterSpacing: theme.spacing(1),
    paddingBottom: theme.spacing(4),
  },
  typodesc: {
    color: "#919191",
    textTransform: "uppercase",
    // fontWeight: "bold",
    fontSize: "1em",
    [theme.breakpoints.up("sm")]: {
      fontSize: "1.2em",
    },
    fontFamily: "Montserrat, sans-serif",
  },
  butcon: {
    width: "20%",
    minWidth: "200px",
    display: "flex",
    justifyContent: "left",
    // backgroundColor: "#ff4e00",
    // backgroundImage: "linear-gradient(90deg, #ff4e00 0%, #ec9f05 74%)",
  },
  butitm: {
    borderRadius: "2rem",
  },
  shadow: {
    height: "100vh",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  textrespo: {
    [theme.breakpoints.down("sm")]: {
      fontSize: ".8rem !important",
    },
  },
  space: {
    padding: theme.spacing(2, 0),
  },
  pic: {
    width: "10rem",
    height: "10rem",
  },
  defaultlogo: {
    width: "10rem",
    height: "10rem",
  },
  ppic: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  cbg: {
    width: "100%",
    backgroundColor: "#5A1186",
    boxShadow: "inset 0px 0px 10px 0px black",
  },
  typ: {
    fontFamily: "Montserrat, sans-serif",
    textTransform: "uppercase",
    fontWeight: "bold",
    opacity: 0.8,
    [theme.breakpoints.down("sm")]: {
      fontSize: "1em !important",
    },
  },
  index: {
    zIndex: "1000",
    "& *": {
      zIndex: "1000",
    },
  },
  typing: {
    display: "inherit",
  },
  cursor: {
    color: "#ee5522",
  },
  bordershadow: {
    boxShadow: "inset 0px 7px 3px -3px rgba(50, 50, 50, 0.75)",
    height: "10px",
    backgroundColor: bgcolor,
  },
}));
export default useStyles;
