import { React, useEffect, useState } from "react";
import {} from "./component/buttns";
import { BrowserRouter as Router } from "react-router-dom";
import Header from "./component/Header/Header";
// import Footer from "./component/Footer";
import AppHead from "./AppHead";
import useToken from "./fetchingData/Chook";
import { useForm, useStep } from "react-hooks-helper";
import { useHistory } from "react-router-dom";

import { dbip } from "./fetchingData/fatchdata";
const steps = [
  { id: "About your Account", indx: 0 },
  { id: "Startup Description", indx: 1 },
  { id: "Market/Intellectual property ", indx: 2 },
  { id: "Financials/Co-founding team", indx: 3 },
  { id: "Preview", indx: 4 },
];

function AppBody(props) {
  let history = useHistory();
  const me = async () => {
    if (token) {
      const res = await fetch(`${dbip}/api/v1/auth/me`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
      });
      const data = await res.json();
      console.log(data);
      if (data.success) {
        localStorage.setItem("user", JSON.stringify(data.data));
        setData(data.data);
      } else {
        window.location.href = "/";
        localStorage.clear();
      }
    }
    // return(await res.json())
  };

  const auth = async () => {
    const Ntask = {
      email: lemail,
      password: lpassword,
    };
    const res = await fetch(`${dbip}/api/v1/auth/login`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(Ntask),
    });
    res.json().then((data) => {
      if (data.success) {
        GetToken(data.token);
        setData(data.data);
        localStorage.setItem("user", JSON.stringify(data.data));
        setLoading(false);
      } else {
        setLoading(false);
        localStorage.clear();
        setErrors({ lemail: "Incorrect info", lpassword: "Incorrect info" });
      }
    });
  };

  const defaultValues = {
    FirstName: "",
    LastName: "",
    username: "",
    password: "",
    Rpassword: "",
    email: "",
    Sname: "",
    website: "",
    epitch: "",
    sds: "Is",
    dq1: "",
    dq2: "",
    dq3: "",
    dq4: "",
    dq5: "",
    dq6: "",
    dq7: "",
    dq8: "",
    dq9: "",
    dq10: "",
    dq11: "",
    dq12: "",
    dq13: "",
    dq14: "",
    dq15: "",
    dq16: "",
    dq17: "",
    dq18: "",
    dq19: "",
    dq20: "",
    dq21: "",
    dq22: "",
    dq23: "",
    dq24: "",
    lpassword: "",
    lemail: "",
  };

  const [
    {
      FirstName,
      LastName,
      username,
      password,
      Rpassword,
      email,
      Sname,
      website,
      epitch,
      sds,
      dq1,
      dq2,
      dq3,
      dq4,
      dq5,
      dq6,
      dq7,
      dq8,
      dq9,
      dq10,
      dq11,
      dq12,
      dq13,
      dq14,
      dq15,
      dq16,
      dq17,
      dq18,
      dq19,
      dq20,
      dq21,
      dq22,
      dq23,
      dq24,
      lemail,
      lpassword,
    },
    setValue,
  ] = useForm(defaultValues);

  const [Loading, setLoading] = useState(false);
  const [data, setData] = useState({});

  const { token, GetToken } = useToken();

  const [open, setOpen] = useState(false);
  const [startup, setStartup] = useState([]);
  const [startups, setStartups] = useState([]);

  const { step, navigation } = useStep({
    steps,
    initialStep: 0,
  });
  const [pictures, setPic] = useState({});
  const [prevpic, setPPic] = useState("");

  const [Phone, setPhone] = useState("");
  const onDrop = (picture, file) => {
    setPic(picture[0]);
    setPPic(file);
  };
  const [Serrors, setErrors] = useState({});
  useEffect(() => {
    const token = localStorage.getItem("token");
    const ldata = JSON.parse(localStorage.getItem("user"));
    if (!token) localStorage.clear();
    if (token && !ldata) me();
    else setData(ldata);
  }, []);

  return (
    <>
      <Router basename={process.env.PUBLIC_URL}>
        <Header token={token} open={open} data={data} />
        <AppHead
          data={data}
          lemail={lemail}
          lpassword={lpassword}
          startup={startup}
          setStartup={setStartup}
          startups={startups}
          setStartups={setStartups}
          token={token}
          open={open}
          setOpen={setOpen}
          auth={auth}
          GetToken={GetToken}
          Loading={Loading}
          setLoading={setLoading}
          username={username}
          FirstName={FirstName}
          LastName={LastName}
          email={email}
          password={password}
          Rpassword={Rpassword}
          Sname={Sname}
          epitch={epitch}
          website={website}
          setData={setData}
          sds={sds}
          dq1={dq1}
          dq2={dq2}
          dq3={dq3}
          dq4={dq4}
          dq5={dq5}
          dq8={dq8}
          dq6={dq6}
          dq7={dq7}
          dq9={dq9}
          dq10={dq10}
          dq11={dq11}
          dq12={dq12}
          dq13={dq13}
          dq14={dq14}
          dq15={dq15}
          dq16={dq16}
          dq17={dq17}
          dq18={dq18}
          dq19={dq19}
          dq20={dq20}
          dq21={dq21}
          dq22={dq22}
          dq23={dq23}
          dq24={dq24}
          setValue={setValue}
          Serrors={Serrors}
          setErrors={setErrors}
          setPic={onDrop}
          step={step}
          steps={steps}
          navigation={navigation}
          Phone={Phone}
          setPhone={setPhone}
          prevpic={prevpic}
          setPPic={setPPic}
          pictures={pictures}
        />
      </Router>
    </>
  );
}

export default AppBody;
