import { correction } from "../../inputdb/correction";
import Typography from "@material-ui/core/Typography";
import Slide from "./slider";
import Button from "@material-ui/core/Button";
import useStyles from "../../styling/style";
import { useEffect, useState } from "react";
import fetchips from "../../fetchingData/fatchdata";
import CircularProgress from "@material-ui/core/CircularProgress";
import { useHistory, useLocation } from "react-router";
import { Box, Container, Divider } from "@material-ui/core";
import defaultlogo from "../../imgs/user.svg";
import PutInputs from "../Inputs";
import { judge } from "../../inputdb/judge";
import Link from "@material-ui/core/Link";
import { dbip } from "../../fetchingData/fatchdata";
import { useForm } from "react-hooks-helper";
import GetIputs from "../inputconditions";
import { useCorrection, useFbSr } from "../../fetchingData/Chook";
const LabelSlider = (props) => {
  const classes = useStyles();
  const data = props.indata;
  // console.log(data);
  const dqs = [
    data?.form.dq1,
    data?.form.dq2,
    data?.form.dq3,
    data?.form.dq4,
    data?.form.dq5,
    data?.form.dq6,
    data?.form.dq7,
    data?.form.dq8,
    data?.form.dq9,
    data?.form.dq10,
    data?.form.dq11,
    data?.form.dq12,
    data?.form.dq13,
    data?.form.dq14,
    data?.form.dq15,
    data?.form.dq16,
    data?.form.dq17,
    data?.form.dq18,
    data?.form.dq19,
    data?.form.dq20,
    data?.form.dq21,
    data?.form.dq22,
    data?.form.dq23,
    data?.form.dq24,
  ];
  const logo =
    data?.logo === "no-photo.jpg"
      ? undefined
      : `${dbip}/api/v1/startups/photo/` + data?.logo;
  return (
    <>
      <Link href={data?.website ? data?.website : ""}>
        <Typography
          variant="h5"
          paragraph
          align="center"
          className={classes.typ}
        >
          {data?.Sname}
        </Typography>
      </Link>
      <div className={classes.ppic}>
        {logo ? (
          //  <img src={logo} className={classes.pic} alt='img' />
          <div
            style={{ background: `url(${logo})  no-repeat  center/contain ` }}
            className={classes.pic}
          ></div>
        ) : (
          <img src={defaultlogo} className={classes.defaultlogo} alt="img" />
        )}
      </div>
      {dqs.map((dq, index) => (
        <div className={classes.space} key={index + 400}>
          {correction[index].divider && correction[index].dtype === "up" ? (
            <Box
              display="flex"
              justifyContent="space-between"
              alignItems="center"
              paddingBottom="2rem"
            >
              <Typography
                className={classes.formhead}
                variant="h6"
                style={{ paddingBottom: 0, paddingRight: "1rem" }}
              >
                {correction[index].divider}
              </Typography>
              {!correction[index].nodivider ? (
                <Divider style={{ flexGrow: 1 }} />
              ) : (
                ""
              )}
            </Box>
          ) : (
            ""
          )}
          <Typography
            paragraph
            key={index + 100}
            variant="subtitle1"
            className={classes.textrespo}
            style={{
              fontFamily: "Montserrat, sans-serif",
              textTransform: "uppercase",
              fontWeight: "bold",
              opacity: 0.8,
              color: "#343f56",
            }}
          >
            {correction[index].id} - {correction[index].name}
          </Typography>
          <Typography
            key={index + 200}
            className={classes.textrespo}
            variant="body1"
            align="left"
            paragraph
            style={{
              fontFamily: "Montserrat, sans-serif",
            }}
          >
            - {dq}
          </Typography>
          <Slide
            key={index + 300}
            index={index}
            correction={correction}
            {...props}
          />
        </div>
      ))}
    </>
  );
};

export default function Correction(props) {
  const [loading, setLoading] = useState(true);
  const location = useLocation();
  const id = location.state?.id;
  const uid = props.udata.id;
  useEffect(() => {
    const GetCorr = async () => {
      const data = await fetch(`${dbip}/api/v1/correction/${id}`, {
        method: "GET",
        headers: {
          "Content-type": "application/json",
          Authorization: `Bearer ${props.token}`,
        },
      });
      const res = data.json().then((datta) => {
        setData(datta);
        putCorr(retrans(datta.mentor?.note));
        putFbSr(datta.mentor?.fb, datta.mentor?.sr);
        setLoading(false);
      });
    };
    if (id) GetCorr();
    else {
      history.push("/Account/startups");
      props.setWAlert({ state: true, msg: "Wrong way to get Evalution" });
      setTimeout(() => {
        props.setWAlert({ state: false, msg: "Wrong way to get Evalution" });
      }, 2000);
    }

    window.scroll(0, 0);
  }, [props.token]);

  const transdata = (cqs) => {
    const obj = {};
    let finalGrade = false;
    let total = 0;
    const res = cqs.map((elem) => {
      obj[Object.keys(elem)[0]] = Object.values(elem)[0];
      if(Object.values(elem)[0] !== -1)
        total++;
    });
    for (const property in obj) {
      if (obj[property] === -1) {
        finalGrade = false;
        break;
      }
      finalGrade = true;
    }
    return { obj, finalGrade , total};
  };

  const retrans = (obj) => {
    const corrarray = [
      "cq1",
      "cq2",
      "cq3",
      "cq4",
      "cq5",
      "cq6",
      "cq7",
      "cq8",
      "cq9",
      "cq10",
      "cq11",
      "cq12",
      "cq13",
      "cq14",
      "cq15",
      "cq16",
      "cq17",
      "cq18",
      "cq19",
      "cq20",
      "cq21",
      "cq22",
      "cq23",
      "cq24",
    ];
    const res = corrarray.map((elem) => {
      return { [elem]: obj[elem] };
    });
    return res;
  };

  const SendCorr = async (corr) => {
    const { obj, finalGrade, total } = transdata(corr);
    console.log(total);
    let fg = fb === "" || sr === "" ? false : finalGrade;
    const send = {
      note: obj,
      fb: fb,
      sr: sr,
      total: total,
      finalGrade: fg,
    };
    // console.log(send);
    const data = await fetch(`${dbip}/api/v1/correction/${id}`, {
      method: "PUT",
      headers: {
        "Content-type": "application/json",
        Authorization: `Bearer ${props.token}`,
      },
      body: JSON.stringify(send),
    });

    // setStartups(data);
    data.json().then((elem) => {
      history.push("/Account/startups");
      if (elem.success) {
        props.setSAlert({ state: true, msg: "Evaluation Submited" });
        setTimeout(() => {
          props.setSAlert({ state: false, msg: "Evaluation Submited" });
        }, 2000);
      } else {
        props.setWAlert({ state: true, msg: "Evaluation Already Submited" });
        setTimeout(() => {
          props.setWAlert({ state: false, msg: "Evaluation Already Submited" });
        }, 2000);
      }
    });
  };

  const { cqs, setCorrection, putCorr } = useCorrection();
  const { fb, sr, GetFbSr, putFbSr } = useFbSr();
  const history = useHistory();

  const [indata, setData] = useState();
  const [note, setNote] = useState({});

  return (
    <>
      {loading ? (
        <Box align="center">
          <CircularProgress />,
        </Box>
      ) : (
        <>
          <LabelSlider
            indata={indata}
            cqs={cqs}
            setCorrection={setCorrection}
          />
          <div style={{ display: "flex", flexDirection: "column" }}>
            <GetIputs
              stype="inelab"
              // judging={true}
              input={judge[0]}
              btn="none"
              {...props}
              fb={fb}
              setValue={GetFbSr}
              rev={true}
            />
            <GetIputs
              stype="inelab"
              // judging={true}
              input={judge[1]}
              btn="none"
              {...props}
              sr={sr}
              // rev={true}
              setValue={GetFbSr}
            />
          </div>
          <div style={{ display: "flex", justifyContent: "flex-end" }}>
            <Button
              onClick={() => {
                SendCorr(cqs);
              }}
              variant="contained"
              style={{
                width: "10%",
                backgroundColor: "#4caf50",
                color: "white",
              }}
            >
              Submit
            </Button>
          </div>
        </>
      )}
    </>
  );
}
