import "./App.css";
import AppBody from "./Content/AppBody.js";
import {
  MuiThemeProvider,
  createTheme,
  makeStyles,
} from "@material-ui/core/styles";
import { useEffect, useState } from "react";
import Loading from "./Content/component/Loading";

const theme = createTheme({
  palette: {
    primary: {
      main: "#251056",
      contrastText: "white",
    },
    secondary: {
      main: "#dc004e",
      contrastText: "#343f56",
    },
  },
});

function App() {
  const [opacity, setOpacity] = useState(0);
  const [display, setDisplay] = useState("block");
  useEffect(() => {
    window.addEventListener("load", (event) => {
      var image = document.querySelector("img");
      var isLoaded = image.complete && image.naturalHeight !== 0;
      if (isLoaded) {
        setDisplay("none");
        setOpacity(1);
      }
    });
  }, []);
  return (
    <>
      <Loading display={display} />
      <div className="bg" style={{ opacity: opacity }}>
        <MuiThemeProvider theme={theme}>
          <AppBody />
        </MuiThemeProvider>
      </div>
    </>
  );
}

export default App;
