import React from "react";
import PropTypes from "prop-types";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import Slider from "@material-ui/core/Slider";
import Typography from "@material-ui/core/Typography";
import Tooltip from "@material-ui/core/Tooltip";
import { Box } from "@material-ui/core";
import InfoIcon from "@material-ui/icons/Info";
import { useState, useEffect } from "react";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    justifyContent: "center",
    alignItems: "flex-start",
    flexDirection: "column",
    padding: theme.spacing(3, 0),
  },
  margin: {
    height: theme.spacing(3),
  },
  typo: {
    opacity: 0.8,
    marginBottom: "0",
    alignSelf: "center",
  },
  info: {
    opacity: 0.8,
    marginBottom: "0",
    marginLeft: 8,
    color: theme.palette.info.dark,
  },
}));



function ValueLabelComponent(props) {
  const { children, open, value } = props;

  return (
    <Tooltip open={open} enterTouchDelay={0} placement="top" title={value}>
      {children}
    </Tooltip>
  );
}

ValueLabelComponent.propTypes = {
  children: PropTypes.element.isRequired,
  open: PropTypes.bool.isRequired,
  value: PropTypes.number.isRequired,
};

const PrettoSlider = withStyles((theme) => ({
  root: {
    color: theme.palette.success.main,
    height: 20,

    width: "99%",
  },
  thumb: {
    height: 30,
    width: 30,
    backgroundColor: "#fff",
    border: "2px solid currentColor",
    marginTop: -6.5,
    marginLeft: -12,
    "&:focus, &:hover, &$active": {
      boxShadow: "inherit",
    },
    [theme.breakpoints.down("sm")]: {
      height: 25,
      width: 25,
    },
  },
  active: {},
  valueLabel: {
    left: "calc(-50% + 10px)",
    [theme.breakpoints.down("sm")]: {
      left: "calc(-50% + 6px)",
    },
  },
  track: {
    height: 20,
    [theme.breakpoints.down("sm")]: {
      height: 15,
    },
    borderRadius: "2rem",
  },
  rail: {
    height: 20,
    [theme.breakpoints.down("sm")]: {
      height: 15,
    },
    borderRadius: "2rem",
  },
}))(Slider);

export default function Slide(props) {
// console.log(eval("props.cqs[props.index]."+props.correction[props.index].corr));
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <Box
        display="flex"
        justifyContent="center"
        alignItems="center"
        width="100%"
        marginTop="16px"
      >
        <Typography
          variant="caption"
          align="center"
          color="textSecondary"
          className={classes.typo}
          paragraph
        >
          Rate it From 0 (Failed) to 10 (Excelent)
        </Typography>
        <Tooltip title={"lalalalallll"} placement="right">
          <InfoIcon className={classes.info} />
        </Tooltip>
      </Box>
      <PrettoSlider
        valueLabelDisplay="auto"
        aria-label="pretto slider"
        step={1}
        min={0}
        max={10}
        name={props.correction[props.index].corr}
        onChange={(event, newValue) => {
          props.setCorrection(newValue, props.correction[props.index].corr);
        }}
        value={eval("props.cqs[props.index]."+props.correction[props.index].corr)}
      />
    </div>
  );
}
