import { makeStyles } from "@material-ui/core/styles";
import Butns from "./buttns";
import GetIputs from "./inputconditions";
import GetPreview from "./Getpreview";

const useStyles = makeStyles((theme) => ({
  form: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    width: "100%",
    alignItems: "center",
    "& > *": {
      margin: theme.spacing(1),
      width: "100%",
    },
  },
  griding_last_child_nth_child_3n___2: { gridColumn: "span 3" },
  griding: {
    display: "grid",
    gridTemplateColumns: "repeat(1, 1fr)",
    [theme.breakpoints.up("sm")]: {
      gridTemplateColumns: "repeat(2, 1fr)",
    },
    columnGap: theme.spacing(8),
    rowGap: theme.spacing(3),
    flexWrap: "wrap",
    paddingBottom: theme.spacing(3),
    paddingTop: theme.spacing(3),
    gridColumn: "span 3"
  },
}));
function PutInputs(props) {
  const classes = useStyles();
  const { inputs } = props;
  return (
    <div className={props.form !== "none" ? classes.form : classes.griding}>
      {inputs.map((input) => {
        return props.disType === "out" ? (
          <GetPreview key={input.id + 100} input={input} {...props} />
        ) : (
          <GetIputs
            key={input.id + 100}
            input={input}
            stype={props.stype}
            {...props}
          />
        );
      })}
      {props.btn !== "none" ? <Butns {...props} /> : ""}
    </div>
  );
}
export default PutInputs;
