/* eslint no-eval: 0 */
import { useEffect } from "react";
import TextField from "@material-ui/core/TextField";
import useStyles from "../styling/inputstyle";
import ImageUploader from "react-images-upload";
import { Divider, Typography } from "@material-ui/core";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import "react-phone-number-input/style.css";
import PhoneInput from "react-phone-number-input";
import AOS from "aos";
import "aos/dist/aos.css";
import { Box } from "@material-ui/core";
import "../../App.css";

function GetIputs(props) {
  useEffect(() => {
    AOS.init({
      once: true,
    });
  }, []);

  const classes = useStyles();
  const { input, setValue, Phone, setPhone } = props;
  if (input.type === "divider") {
    return (
      <Box
        display="flex"
        justifyContent="space-between"
        alignItems="center"
        padding="2rem"
      >
        <Typography
          className={classes.formhead}
          style={{ paddingBottom: 0, paddingRight: "1rem" }}
        >
          {input.name}
        </Typography>
        <Divider style={{ flexGrow: 1 }} />
      </Box>
    );
  }
  if (input.type === "phone") {
    return (
      <>
        <PhoneInput
          data-aos="fade-up"
          data-aos-duration="1000"
          placeholder="Enter phone number"
          name={input.value}
          value={Phone}
          defaultCountry="MA"
          onChange={setPhone}
        />
        {props.Serrors !== undefined ? (
          Object.keys(props.Serrors).length > 0 ? (
            eval("props.Serrors." + input.value) === "" ? (
              ""
            ) : (
              <Typography
                data-aos="fade-up"
                data-aos-duration="1000"
                variant="caption"
                color="error"
                style={{ marginTop: "0px", paddingLeft: "7em" }}
              >
                Phone number format error *
              </Typography>
            )
          ) : (
            ""
          )
        ) : (
          ""
        )}
      </>
    );
  }
  if (input.name === "uplogo") {
    return (
      <ImageUploader
        withIcon={true}
        buttonText="Choose images"
        onChange={props.setPic}
        name={input.name}
        imgExtension={[".jpg", ".gif", ".png", ".gif"]}
        maxFileSize={5242880}
        singleImage={true}
        withPreview={true}
        // required={true}
        label={"You Can Upload Your Logo later, Not required"}
        labelStyles={{ color: "orange", textAlign: "center" }}
        data-aos="fade-up"
        data-aos-duration="1000"
      />
    );
  }
  if (input.radio) {
    return (
      <>
        <Typography
          className={classes.textspan}
          data-aos="fade-up"
          data-aos-duration="1000"
        >
          {input.name}
        </Typography>
        <RadioGroup
          aria-label="blablaa"
          name={input.value}
          value={eval("props." + input.value)}
          onChange={setValue}
          key="radio"
        >
          {input.options.map((elem, index) => (
            <FormControlLabel
              value={elem}
              control={<Radio color="primary" />}
              label={elem}
              key={index}
              disabled={input.preview}
            />
          ))}
        </RadioGroup>
      </>
    );
  }
  if (props.stype !== "inelab") {
    if (input.multiline)
      return (
        <TextField
          data-aos="fade-up"
          data-aos-duration="1000"
          size="small"
          key={input.id}
          label={input.name}
          type={input.type}
          multiline={input.multiline}
          rows={input.row}
          name={input.value}
          value={eval("props." + input.value)}
          onChange={setValue}
          variant="outlined"
          required={true}
          disabled={input.preview}
          error={
            props.Serrors !== undefined
              ? Object.keys(props.Serrors).length > 0
                ? eval("props.Serrors." + input.value) === ""
                  ? false
                  : true
                : false
              : false
          }
          helperText={
            props.ht && props.Serrors === undefined
              ? input.ht
              : props.Serrors !== undefined
              ? Object.keys(props.Serrors).length > 0
                ? eval("props.Serrors." + input.value) === ""
                  ? false
                  : eval("props.Serrors." + input.value)
                : false
              : false
          }
        />
      );
    else
      return (
        <TextField
          size="small"
          data-aos="fade-up"
          data-aos-duration="1000"
          key={input.id}
          label={input.name}
          type={input.type}
          name={input.value}
          value={eval("props." + input.value)}
          onChange={setValue}
          variant="outlined"
          disabled={input.preview}
          required={true}
          error={
            props.Serrors !== undefined
              ? Object.keys(props.Serrors).length > 0
                ? eval("props.Serrors." + input.value) === ""
                  ? false
                  : true
                : false
              : false
          }
          helperText={
            props.ht && props.Serrors === undefined
              ? input.ht
              : props.Serrors !== undefined
              ? Object.keys(props.Serrors).length > 0
                ? eval("props.Serrors." + input.value) === ""
                  ? false
                  : eval("props.Serrors." + input.value)
                : false
              : false
          }
        />
      );
  } else {
    return (
      <div className={classes.divinelab}>
        {/* <span className={classes.textspan}></span> */}
        <Typography
          className={classes.textspan}
          data-aos="fade-up"
          data-aos-duration="1000"
        >
          {input.name}
        </Typography>
        <TextField
          size="small"
          data-aos="fade-up"
          data-aos-duration="1000"
          key={input.id}
          type={input.type}
          multiline={input.multiline}
          rows={input.row}
          name={input.value}
          value={eval("props." + input.value)}
          onChange={setValue}
          required={true}
          variant="outlined"
          disabled={input.preview}
          error={
            props.Serrors !== undefined
              ? Object.keys(props.Serrors).length > 0
                ? eval("props.Serrors." + input.value) === ""
                  ? false
                  : true
                : false
              : false
          }
          helperText={
            input.ht
              ? input.ht
              : props.Serrors !== undefined
              ? Object.keys(props.Serrors).length > 0
                ? eval("props.Serrors." + input.value) === ""
                  ? false
                  : eval("props.Serrors." + input.value)
                : false
              : false
          }
        />
      </div>
    );
  }
}
export default GetIputs;
