// import Container from "@material-ui/core/Container";
// import Typography from "@material-ui/core/Typography";*

import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";
// import logo from "../../imgs/social.jpg";
import { useEffect } from "react";
import Avatar from "@material-ui/core/Avatar";
import SearchOutlinedIcon from "@material-ui/icons/SearchOutlined";
import HttpsOutlinedIcon from "@material-ui/icons/HttpsOutlined";
import FavoriteBorderOutlinedIcon from "@material-ui/icons/FavoriteBorderOutlined";
import { Container, Typography } from "@material-ui/core";
import "aos/dist/aos.css";
import AOS from "aos";

const useStyles = makeStyles((theme) => ({
  root: {
    padding: theme.spacing(6),
    // boxShadow: "0px -4px 3px rgba(50, 50, 50, 0.75)",
    [theme.breakpoints.up("md")]: {
      padding: theme.spacing(0, 4),
    },
    minHeight: "50vh",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    backgroundColor: "rgb(247, 249, 252)",
  },
  bg: {
    color: "#f44336",
    backgroundColor: "#f443361f",
    width: theme.spacing(8),
    height: theme.spacing(8),
    marginBottom: theme.spacing(1),
  },
  title: {
    margin: "0px 0px 0.35em",
    fontFamily: "Inter, sans-serif",
    fontSize: "1.25rem",
    lineHeight: "1.6",
    textAlign: "center",
    fontWeight: 500,
    color: "rgb(45, 55, 72)",
  },
  desc: {
    margin: "0px",
    fontFamily: "Inter, sans-serif",
    fontWeight: "400",
    fontSize: "1rem",
    lineHeight: 1.5,
    textAlign: "center",
    color: "rgb(100, 110, 115)",
  },
  logo: {
    fontSize: "28",
  },
}));

export const Whatwedo = () => {
  useEffect(() => {
    AOS.init({ once: false });
  }, []);
  const classes = useStyles();
  const cards = [
    {
      name: "What a day",
      logo: <SearchOutlinedIcon className={classes.logo} />,
      desc: "Designed with the latest design trends in mind. Webbee feels modern, minimal, and beautiful.",
    },
    {
      name: "Why u even here",
      logo: <HttpsOutlinedIcon className={classes.logo} />,
      desc: "Designed with the latest design trends in mind. Webbee feels modern, minimal, and beautiful.",
    },
    {
      name: "Searching for something",
      logo: <FavoriteBorderOutlinedIcon className={classes.logo} />,
      desc: "Designed with the latest design trends in mind. Webbee feels modern, minimal, and beautiful.",
    },
  ];
  return (
    <Container className={classes.root} maxWidth="lg">
      <Grid container spacing={6} justifyContent="center" align="center" >
        {cards.map((elem, index) => (
          <Grid item key={index} xs={12} md={4} >
            <Avatar className={classes.bg} data-aos="fade-up" data-aos-delay={(index * 100).toString()}>{elem.logo}</Avatar>
            <Typography variant="h6" className={classes.title} data-aos="fade-up" data-aos-delay={(index * 100).toString()}>
              {elem.name}
            </Typography>
            <Typography variant="body1" className={classes.desc} data-aos="fade-up" data-aos-delay={(index * 100).toString()}>
              {elem.desc}
            </Typography>
          </Grid>
        ))}
      </Grid>
    </Container>
  );
};
