import useStyles from "../../styling/style";
import { React, useEffect } from "react";
import Typography from "@material-ui/core/Typography";
import { useHistory } from "react-router-dom";
import Button from "@material-ui/core/Button";
import Footer from "../Footer";
import { Getintouch } from "./Getintouch";
import { withStyles } from "@material-ui/core/styles";
import { Container, Divider, Grid } from "@material-ui/core";
import { Featuers } from "./Features";
import { Whatwedo } from "./whatwedo";
import { Getstarted } from "./Getstarted";
import AOS from "aos";
import Typist from "react-text-typist";
import "aos/dist/aos.css";

function Home(props) {
  const redirect = () => {
    if (props.token) history.push("/Account");
  };

  useEffect(() => {
    redirect();
    AOS.init({ once: false });
  }, []);

  const ColorButton = withStyles((theme) => ({
    root: {
      fontFamily: "Montserrat, sans-serif",
      fontWeight: "bold",
      color: theme.palette.error.main,
      border: theme.palette.error.main + " 1px solid",
      "&:hover": {
        transition: theme.transitions.create(
          ["background-color", "fontWeight", "color", "letter-spacing"],
          {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.complex,
          }
        ),
        color: "#FFFFFF",
        fontWeight: "bold",
        letterSpacing: 2,
        backgroundColor: theme.palette.error.main,
      },
    },
  }))(Button);
  const history = useHistory();
  const classes = useStyles();
  return (
    <>
      <div className={classes.container}>
        <div className={classes.over}></div>
        <div className={classes.home}>
          <Container maxWidth="xl" align="center" justify="center">
            <Container maxWidth="xl" align="center" justify="center">
              <Grid container align="left" justifyContent="center">
                <Grid
                  item
                  xs={12}
                  md={6}
                  lg={4}
                  style={{ alignSelf: "center" }}
                >
                  <div>
                    <span
                      className={classes.typo}
                      color="primary"
                      style={{ color: "white" }}
                    >
                      {"Turn your ideas into a "}
                      <span style={{ color: "#ee5522" }}>
                        <Typist
                          sentences={["Startup.", "Future.", "Sucsses."]}
                          loop={true}
                          pauseTime={2000}
                          cursorSmooth={true}
                        />
                      </span>
                    </span>
                  </div>
                  <Typography
                    data-aos="zoom-in"
                    data-aos-duration="1100"
                    variant="h6"
                    className={classes.typodesc}
                    align="left"
                    paragraph
                  >
                    Accelerate the economic and human development of the African
                    continent, in a sustainable and responsible perspective
                    through applied research, innovation and entrepreneurship.
                  </Typography>
                  <div className={classes.butcon}>
                    <ColorButton
                      fullWidth
                      data-aos="fade-up"
                      data-aos-duration="1500"
                      size="large"
                      // color="secondary"
                      variant="outlined"
                      className={classes.butitm}
                      onClick={() => {
                        history.push("/Apply");
                      }}
                    >
                      Apply Now
                    </ColorButton>
                  </div>
                </Grid>
                <Grid item md={6} lg={8}>
                  {/* <img alt="" src={rocket} style={{ opacity: 0.2 }} /> */}
                </Grid>
              </Grid>
            </Container>
          </Container>
        </div>
        <div className={classes.bordershadow}></div>
        <div>
          <Whatwedo />
          <Featuers />
          <Getintouch />
          <Getstarted />
        </div>
      </div>
      <Divider />
      <Footer />
    </>
  );
}
export default Home;
