import { React, useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { useStep } from "react-hooks-helper";
import Inscription from "./component/inscription.js";
import { useForm } from "react-hooks-helper";
import Home from "./component/Home/Home";
import { Contactus } from "./component/Home/Contactus/Contactus";
import Signin from "./forms/Signin";
import { Switch, Route, Redirect } from "react-router-dom";
import NotFound from "./component/NotFound.js";
import Dashboard from "./component/dashboard/Dashboard";
import { Container } from "@material-ui/core";
import { dbip } from "./fetchingData/fatchdata";
import { useHistory, useLocation } from "react-router-dom";
import {
  checkSignup,
  checkDgeneral,
  checkDdescription,
  checkFindustry,
} from "./fetchingData/errortootls";

function AppHead(props) {
  let history = useHistory();
  let location = useLocation();

  // const redirect = () => {
  //   if(token)
  //    history.push("/Account")
  // }
  // useEffect(() => {
  //   if(!token)
  //   {
  //     localStorage.clear();
  //     history.push("/signin")
  //   }
  // }, [])

  const { token, GetToken } = props;



  return (
    <Switch>
      <Route exact path="/">
        <Home  token={token}/>
      </Route>
      <Route exact path="/Apply">
        <Container maxWidth="xl">
          <Inscription
            token={token}
            checkSignup={checkSignup}
            checkDgeneral={checkDgeneral}
            checkDdescription={checkDdescription}
            checkFindustry={checkFindustry}
            {...props}
          />
        </Container>
      </Route>
      <Route exact path="/signin">
        <Signin
          ftype="LOGIN"
          Serrors={{}}
          login="login"
          GetToken={GetToken}
          token={token}
          Loading={props.Loading}
          setLoading={props.setLoading}
          auth={props.auth}
          {...props}
        />
      </Route>
      <Route path="/Account">
          <Dashboard
            open={props.open}
            setOpen={props.setOpen}
            startup={props.startup}
            setStartup={props.setStartup}
            startups={props.startups}
            setStartups={props.setStartups}
            token={token}
            data={props.data}
          />
      </Route>
      <Route path="/Contact">
        <Contactus token={token}
 />
      </Route>
      <Route component={NotFound} />
    </Switch>
  );
}
export default AppHead;
