import { Container, Paper } from "@material-ui/core";
import { addmentor } from "../../inputdb/addmentor";
import PutInputs from "../Inputs";
import { React, useEffect, useState } from "react";
import { useForm } from "react-hooks-helper";
import Alert from "@material-ui/lab/Alert";
import Slide from "@material-ui/core/Slide";
import { dbip } from "../../fetchingData/fatchdata";
export const AddMentor = () => {
  const createuser = async () => {
    const token = localStorage.getItem("token");
    const ntask = {
      firstname: FirstName,
      lastname: LastName,
      email: email,
      role: role,
      phone: Phone,
      password: password,
    };
    const res = await fetch(`${dbip}/api/v1/users`, {
      method: "POST",
      headers: {
        "Content-type": "application/json",
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify(ntask),
    });
    // console.log(await res.json());
    const data = await res.json();
    if (!data.success) {
      setWAlert({ state: true, msg: data.error });
      setTimeout(() => {
        setWAlert({ state: false, msg: data.error });
      }, 2000);
    } else {
      setSAlert({ state: true, msg: "user Added" });
      setTimeout(() => {
        setSAlert({ state: false, msg:"user Added" });
      }, 2000);
    }
  };

  const defaultValues = {
    FirstName: "",
    LastName: "",
    username: "",
    password: "",
    email: "",
    role: "",
  };

  const [{ FirstName, LastName, password, email, role }, setValue] =
    useForm(defaultValues);
  const [pictures, setPic] = useState({});
  const [prevpic, setPPic] = useState("");
  const [Salert, setSAlert] = useState({ state: false, msg: "" });
  const [Walert, setWAlert] = useState({ state: false, msg: "" });
  const [Phone, setPhone] = useState("");
  const onDrop = (picture, file) => {
    setPic(picture[0]);
    setPPic(file);
  };

  return (
    <Paper>
      <Slide direction="down" in={Salert.state} mountOnEnter unmountOnExit>
        <Alert severity="success" variant="filled">
          {Salert.msg}
        </Alert>
      </Slide>
      <Slide direction="down" in={Walert.state} mountOnEnter unmountOnExit>
        <Alert severity="warning" variant="filled">
          {Walert.msg}
        </Alert>
      </Slide>
      <Container maxWidth="md">
        <PutInputs
          FirstName={FirstName}
          LastName={LastName}
          email={email}
          password={password}
          setPhone={setPhone}
          setPic={onDrop}
          setValue={setValue}
          role={role}
          Phone={Phone}
          inputs={addmentor}
          ftype="cos"
          btnname="Add mentor"
          btn="none"
          form="none"
          func={() => {}}
        />
        <PutInputs
          inputs={[
            {
              id: 17,
              name: "uplogo",
            },
          ]}
          ftype="cos"
          btnname="Add mentor"
          func={createuser}
        />
      </Container>
    </Paper>
  );
};

export const DeleteMentor = () => {
  return <Paper>Delete</Paper>;
};
