import { makeStyles } from "@material-ui/core/styles";
const drawerWidth = 240;
export const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
  },
  toolbar: {
    paddingRight: 24, // keep right padding when drawer closed
  },
  toolbarIcon: {
    display: "flex",
    alignItems: "center",
    justifyContent: "space-between",
    padding: "0 6px",
    ...theme.mixins.toolbar,
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: 36,
  },
  menuButtonHidden: {
    display: "none",
  },
  title: {
    flexGrow: 1,
  },
  drawerPaper: {
    // backgroundColor: theme.palette.success.main,
    position: "fixed",
    whiteSpace: "nowrap",
    width: drawerWidth,
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerPaperClose: {
    overflowX: "hidden",
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    width: theme.spacing(7),
    [theme.breakpoints.up("sm")]: {
      width: theme.spacing(9),
    },
  },
  appBarSpacer: theme.mixins.toolbar,
  contentClose: {
    flexGrow: 1,

    height: "100%",
    overflow: "auto",
    transition: theme.transitions.create("padding", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    paddingLeft: drawerWidth,
  },
  contentOpen: {
    marginLeft: theme.spacing(7),
    [theme.breakpoints.up("sm")]: {
      marginLeft: theme.spacing(9),
    },
    flexGrow: 1,
    height: "100%",
    overflow: "auto",
    transition: theme.transitions.create("padding", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  container: {
    marginTop: theme.spacing(4),
    // paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
  },
  corcontainer:{
    padding:0,
    marginTop: 0,
    borderRadius: 0,
    [theme.breakpoints.up('md')]:{
      padding: theme.spacing(0,4),
      marginTop: theme.spacing(4),
    }
  },
  paper: {
    padding: theme.spacing(2),
    display: "flex",
    overflow: "auto",
    flexDirection: "column",
    // height:"100%"
    // backgroundColor: theme.palette.success.light
  },
  fixedHeight: {
    // height: 140,
    borderRadius:theme.spacing(2)
  },
  logo: {
    width: "20px",
    [theme.breakpoints.up("sm")]: {
      width: "29.59px",
    },
  },
  spaper: {
    padding: theme.spacing(3),
  },
  bg: {
    backgroundImage: `url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' version='1.1' xmlns:xlink='http://www.w3.org/1999/xlink' xmlns:svgjs='http://svgjs.com/svgjs' width='1440' height='560' preserveAspectRatio='none' viewBox='0 0 1440 560'%3e%3cg mask='url(%26quot%3b%23SvgjsMask1001%26quot%3b)' fill='none'%3e%3cpath d='M379.61 624.69C530.74 572.56 553.97 139.98 781.01 126.91 1008.04 113.84 981.71 196.91 1182.4 196.91 1383.1 196.91 1481.94 127.17 1583.8 126.91' stroke='rgba(178%2c 86%2c 1%2c 0.25)' stroke-width='2'%3e%3c/path%3e%3cpath d='M264.38 599.8C404.13 588.73 497.84 310.41 743.84 308.99 989.84 307.57 983.58 378.99 1223.31 378.99 1463.04 378.99 1581.63 309.17 1702.77 308.99' stroke='rgba(178%2c 86%2c 1%2c 0.25)' stroke-width='2'%3e%3c/path%3e%3cpath d='M630.01 657.64C726.78 638 668.38 407.18 919.35 388.81 1170.32 370.44 1341.41 152.75 1498.03 148.01' stroke='rgba(178%2c 86%2c 1%2c 0.25)' stroke-width='2'%3e%3c/path%3e%3cpath d='M264.41 572.99C462.11 562.61 590.61 220.85 968.75 211.1 1346.9 201.35 1490.7 22.37 1673.1 20.7' stroke='rgba(178%2c 86%2c 1%2c 0.25)' stroke-width='2'%3e%3c/path%3e%3cpath d='M502.54 640.84C642.52 634.95 729.3 413.77 1009.45 401.86 1289.59 389.95 1376.18 167.08 1516.35 161.06' stroke='rgba(178%2c 86%2c 1%2c 0.25)' stroke-width='2'%3e%3c/path%3e%3c/g%3e%3cdefs%3e%3cmask id='SvgjsMask1001'%3e%3crect width='1440' height='560' fill='white'%3e%3c/rect%3e%3c/mask%3e%3c/defs%3e%3c/svg%3e")`,
    backgroundSize: "cover",
  },
  getcard: {
    // minHeight: "calc(100vh - 266px)",
  },
}));
