import { isPossiblePhoneNumber } from "react-phone-number-input";
import { emailchecker, Snamechecker } from "./fatchdata";

export function pV(password) {
    var re = {
      capital: /[A-Z]/,
      digit: /[0-9]/,
      full: /[A-Za-z0-9]{7,13}$/,
    };
    return (
      re.capital.test(password) &&
      re.digit.test(password) &&
      re.full.test(password)
    );
  }

  var pattern = new RegExp(
    /^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i
  );
  var webpat = new RegExp(
    /(https?:\/\/(?:www\.|(?!www))[a-zA-Z0-9][a-zA-Z0-9-]+[a-zA-Z0-9]\.[^\s]{2,}|www\.[a-zA-Z0-9][a-zA-Z0-9-]+[a-zA-Z0-9]\.[^\s]{2,}|https?:\/\/(?:www\.|(?!www))[a-zA-Z0-9]+\.[^\s]{2,}|www\.[a-zA-Z0-9]+\.[^\s]{2,})/i
  );
  export const checkSignup = async (values, Phone, setErrors) => {
    let res = {};
    res.FirstName = !/[a-zA-z]{3,}/.test(values.FirstName)
      ? "This field is required 'more then 3 characters'"
      : "";
    res.LastName = !/[a-zA-z]{3,}/.test(values.LastName)
      ? "This field is required 'more then 3 characters'"
      : "";
    res.email = !pattern.test(values.email)  ? "Inccorect Email Format" : await emailchecker(values.email) ? "Email Already used" : "";
    res.Phone = Phone
      ? !isPossiblePhoneNumber(Phone)
        ? "Inccorect Phone Format"
        : ""
      : "This field is required";
    res.password = pV(values.password) === false ? "7-13 characters" : "";
    res.Rpassword =
      values.Rpassword !== values.password ? "Password Not match" : "";
    res.num = values.num === "" ? "This field is required" : "";
    setErrors(res);
  };
  export const checkDgeneral = async (values, setErrors) => {
    let res = {};
    res.Sname = values.Sname === "" ? "This field is required" : await Snamechecker(values.Sname) ? "Startup Name Already used" : "";
    res.website = !webpat.test(values.website) ? "This field is required" : "";
    res.epitch = values.epitch === "" ? "This field is required" : "";
    res.sds = values.sds === "" ? "This field is required" : "";
    res.dq1 = values.dq1 === "" ? "This field is required" : "";
    res.dq2 = values.dq2 === "" ? "This field is required" : "";
    res.dq3 = values.dq3 === "" ? "This field is required" : "";
    res.dq4 = values.dq4 === "" ? "This field is required" : "";
    res.dq5 = values.dq5 === "" ? "This field is required" : "";
    setErrors(res);
  };
