import React from "react";
import { useTheme } from "@material-ui/core/styles";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  Label,
  ResponsiveContainer,
  Tooltip,
} from "recharts";
import { PieChart, Pie, Sector, Cell } from "recharts";
import { Container, Grid, Typography } from "@material-ui/core";

function createData(time, amount) {
  return { time, amount };
}

const ldata = [
  createData("Jan", 0),
  createData("Feb", 10),
  createData("Mar", 20),
  createData("Apr", 30),
];

export default function Chart() {
  const theme = useTheme();

  return (
    <>
      <Typography component="h2" variant="h6" color="primary" gutterBottom>
        This Year
      </Typography>
      <ResponsiveContainer width="100%" height="100%">
        <LineChart
          data={ldata}
          margin={{
            top: 16,
            right: 16,
            bottom: 0,
            left: 24,
          }}
        >
          <XAxis dataKey="time" stroke={theme.palette.text.secondary} />
          <YAxis stroke={theme.palette.text.secondary}>
            <Label
              angle={270}
              position="left"
              style={{
                textAnchor: "middle",
                fill: theme.palette.text.primary,
              }}
            >
              Startups
            </Label>
          </YAxis>
          <Line
            type="monotone"
            dataKey="amount"
            stroke={theme.palette.primary.main}
            dot={false}
          />
        </LineChart>
      </ResponsiveContainer>
    </>
  );
}

export const Piechart = ({startup , role}) => {
  const data = role === "admin"
  ? ([
    { name: "Recomanded", value: startup?.length },
    { name: "Strongly recomanded", value: startup?.length  },
    { name: "Not recemanded", value: startup?.length  },
    // { name: "Startup Total", value: startup.length  },
  ]) : ([
    { name: "onProgress", value: startup?.length },
    { name: "Evaluated", value: startup?.length  },
    // { name: "Startup Total", value: startup.length  },
  ]);

  const COLORS = ["#0088FE", "#00C49F", "#FFBB28", "#FF8042"];

  const RADIAN = Math.PI / 180;
  const renderCustomizedLabel = ({
    cx,
    cy,
    midAngle,
    innerRadius,
    outerRadius,
    percent,
    index,
  }) => {
    const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
    const x = cx + radius * Math.cos(-midAngle * RADIAN);
    const y = cy + radius * Math.sin(-midAngle * RADIAN);

    return (
      <text
        x={x}
        y={y}
        fill="white"
        textAnchor={x > cx ? "start" : "end"}
        dominantBaseline="central"
      >
        {`${(percent * 100).toFixed(0)}%`}
      </text>
    );
  };
  return (
    <ResponsiveContainer width="100%" height="100%">
      <PieChart width={400} height={400}>
        <Pie
          data={data}
          cx="50%"
          cy="50%"
          labelLine={false}
          label={renderCustomizedLabel}
          outerRadius={80}
          fill="#8884d8"
          dataKey="value"
        >
          {data.map((entry, index) => (
            <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
          ))}
        </Pie>
      <Tooltip/>
      </PieChart>
    </ResponsiveContainer>
  );
};
