import { useStyles } from "./contactStyle";
import { Grid } from "@material-ui/core";
import { Typography } from "@material-ui/core";
import { Social } from "./social";
import { Contactinfo } from "./contactinfo";
import round from "../../../imgs/circle.svg";
// import round2 from "./imgs/circle2.svg";

export const LeftSide = () => {
  const classes = useStyles();
  return (
    
      <Grid item xs={12} md={4} className={classes.col1}>
        <div className={classes.divn}>
        <img src={round} className={classes.circle}  alt="img"/>
        {/* <img src={round2} className={classes.circle2}  alt="img"/> */}
          <Typography variant="h5" align="left" className={classes.typo}>
            Contact information
          </Typography>
          <Typography variant="h6" align="left" className={classes.typo2}>
            Fill up the form and our Team will get back to you within 24 hours
          </Typography>
          <Contactinfo />
        </div>
        <Social />
      </Grid>
    
  );
};
