import { React, useState, useEffect } from "react";
import clsx from "clsx";
import Drawer from "@material-ui/core/Drawer";
import List from "@material-ui/core/List";
import Divider from "@material-ui/core/Divider";
import IconButton from "@material-ui/core/IconButton";
import MenuOpenIcon from "@material-ui/icons/MenuOpen";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import { MainListItems } from "./listItems";
import Logo from "../../imgs/llogodash.svg";
import fetchips from "../../fetchingData/fatchdata";
import { useStyles } from "../../styling/dashbordstyle";
import { InnerBoard } from "./InnerBoard.js";
import Box from "@material-ui/core/Box";
import Copyright from "../copyright";
import { dbip } from "../../fetchingData/fatchdata";
import { useHistory } from "react-router";
import { PeopleSharp } from "@material-ui/icons";

export default function Dashboard({
  setStartup,
  startup,
  setStartups,
  startups,
  token,
  open,
  data,
  setOpen,
}) {
  const classes = useStyles();

  let history = useHistory();
  useEffect(() => {
    const Getstartups = async () => {
      const data = await fetch(`${dbip}/api/v1/correction`, {
        method: "GET",
        headers: {
          "Content-type": "application/json",
          Authorization: `Bearer ${token}`,
        },
      });

      const res = await data.json();

      if (res.success) setStartup(res.data);
      else {
        if (res.error === "Not authorize to access this route...")
          localStorage.clear();
      }
    };
    Getstartups();

    function handleResize() {
      window.innerWidth < 960 ? setOpen(false) : setOpen(true);
    }
    window.addEventListener("resize", handleResize);
    handleResize();
  }, [setStartup]);

  const handleDrawerOpen = () => {
    setOpen(true);
  };
  const handleDrawerClose = () => {
    setOpen(false);
  };
  return (
    <div className={classes.root}>
      <Drawer
        variant="permanent"
        classes={{
          paper: clsx(classes.drawerPaper, !open && classes.drawerPaperClose),
        }}
        open={open}
      >
        <div className={classes.toolbarIcon}>
          <IconButton size="medium" onClick={handleDrawerOpen}>
            <img src={Logo} className={classes.logo} alt="img" />
          </IconButton>
          <IconButton onClick={handleDrawerClose}>
            <MenuOpenIcon color="primary" />
          </IconButton>
        </div>
        <Divider />
        <List><MainListItems/></List>
      </Drawer>
      <main className={!open ? classes.contentOpen : classes.contentClose}>
        <div className={classes.appBarSpacer} />
        <InnerBoard
          startup={startup}
          startups={startups}
          setStartup={setStartup}
          setStartups={setStartups}
          token={token}
          data={data}
        />
        <div className={classes.appBarSpacer} />
        <Box pt={4} className={classes.copy}>
          <Copyright />
        </Box>
      </main>
    </div>
  );
}
