import { React } from "react";
import Grid from "@material-ui/core/Grid";
import "../../App.css";
import PutInputs from "../component/Inputs";
import useStyles from "../styling/inputstyle";
import { preview } from "../inputdb/preview";
import { ppdata } from "../inputdb/Ppdata";
import logo from "../imgs/user.svg";
import Submited from "./Submited";

function Preview(props) {
  // console.log(props.pictures)
  const classes = useStyles();
  switch (props.step.index) {
    case 0:
      return (
        <div className={classes.root}>
          <Grid
            container
            direction="row-reverse"
            alignItems="center"
            spacing={3}
            style={{ padding: "2rem" }}
          >
            <Grid item xs={12}>
              <div className={classes.header}>
                <span className={classes.formhead}>Preview :</span>
              </div>
              <div className={classes.ppic}>
                {props.prevpic && props.prevpic ? (
                  <img src={props.prevpic} className={classes.pic} alt='img' />
                ) : (
                  <img src={logo} className={classes.pic} alt='img'/>
                )}
              </div>
              <PutInputs disType="out" inputs={ppdata} btn="none" form="none" {...props} />
              <PutInputs
                disType="in"
                inputs={preview}
                stype="inelab"
                {...props}
              />
            </Grid>
          </Grid>
        </div>
      );

    case 1:
      return <Submited {...props}/>;
    default:;
  }
}

export default Preview;
