import { React, useEffect } from "react";
// import formsvg from "../imgs/pitch.svg";
import Grid from "@material-ui/core/Grid";
import "../../App.css";
import PutInputs from "../component/Inputs";
import useStyles from "../styling/inputstyle";
import { Step1 } from "../inputdb/Step1";
import AOS from "aos";
import "aos/dist/aos.css";
import { Paper, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core";
// import Link from "@material-ui/core/Link";
import { Link } from "react-router-dom";

const primary = "#251056 !important";
const useStyle = makeStyles((theme) => ({
  root: {
    padding: theme.spacing(3),
    overflow: "hidden",
    minHeight: "80vh",
  },
  col1: {
    display: "flex",
    // [theme.breakpoints.down("sm")]: {
    //   display: "none",
    // },
    backgroundColor: theme.palette.error.main,
    minHeight: "100%",
    boxShadow: "inset 0px 0px 20px 0px #FFFFFF3d",

    [theme.breakpoints.up("sm")]: {
      minHeight: "80vh",
      padding: theme.spacing(8),
    },
    padding: theme.spacing(4),
    // display: "flex",
    justifyContent: "space-between",
    alignItems: "flex-start",
    flexDirection: "column",
    position: "relative",
    overflow: "hidden",
    [theme.breakpoints.up("md")]: {
      borderRadius: "1rem 0 0 1rem",
    },
    borderRadius: " 0 0 1rem 1rem",
  },
  col2: {
    backgroundColor: "#fff",
    minHeight: "80vh",
    position: "relative",
    overflow: "hidden",
    [theme.breakpoints.up("md")]: {
      borderRadius: "1rem",
    },
    borderRadius: "0 0 1rem 1rem",
    [theme.breakpoints.up("sm")]: {
      padding: theme.spacing(8),
    },
    padding: theme.spacing(4),
  },
  txtf: {
    width: "100%",
  },
  txtf2: {
    width: "100%",
    [theme.breakpoints.up("sm")]: {
      width: "90%",
    },
  },
  divn: {
    display: "flex",
    alignItems: "flex-start",
    flexDirection: "column",
    zIndex: 2,
  },
  pap: {
    borderRadius: "1rem !important",
    backgroundColor: "#f5f5f5",
  },
  typo: {
    fontFamily: "Montserrat, sans-serif !important",
    fontWeight: "bold !important",
    textTransform: "uppercase !important",
    color: "white",
    zIndex: 2,
    fontSize: "1rem !important",
  },
  typo2: {
    fontFamily: "Montserrat, sans-serif !important",
    textTransform: "uppercase !important",
    color: "white",
    paddingTop: theme.spacing(2),
    zIndex: 2,
    // fontWeight: "bold !important",
    // fontSize: "0.7rem !important",
    // [theme.breakpoints.up("sm")]: {
    //   fontSize: "0.8rem !important",
    // },
  },
  typo3: {
    fontFamily: "Montserrat, sans-serif !important",
    opacity: 0.9,
    color: primary,
    zIndex: 2,
    fontWeight: "bold !important",
    fontSize: "0.8rem !important",
  },
  circle: {
    position: "absolute",
    bottom: 0,
    opacity: 0.4,
    transform: "translate(-50%, 50%)",
    pointerEvents: "none",
  },
  circle3: {
    position: "absolute",
    top: 0,
    right: 0,
    width: "15em !important",
    transform: "translate(50%, -50%)",
    pointerEvents: "none",
    display: "none",
    [theme.breakpoints.up("md")]: {
      display: "block",
    },
  },
  circle4: {
    position: "absolute",
    bottom: 200,
    right: 150,

    transform: "translate(50%, 50%)",
    pointerEvents: "none",
  },
  circle2: {
    position: "absolute",
    right: 0,
    transform: "translate(0%, 50%)",
    pointerEvents: "none",
  },
  item: {},
  conitem: {
    zIndex: 2,
    paddingTop: theme.spacing(4),
    [theme.breakpoints.up("sm")]: {
      paddingTop: theme.spacing(8),
    },
  },
  consocial: {
    bottom: 0,
    left: 0,
    zIndex: 2,
  },
  icon: {
    color: primary,
    fontSize: "1.5rem !important",
    [theme.breakpoints.up("sm")]: {
      fontSize: "30px !important",
    },
  },
  icon2: {
    color: primary,

    fontSize: "1.5rem !important",
    [theme.breakpoints.up("sm")]: {
      fontSize: "30px !important",
    },
  },
  iconb: {
    backgroundColor: "transparant",
    "&:hover, &:active": {
      backgroundColor: "#FF8A007d !important",
    },
  },
  boxbut: {
    alignSelf: "right",
  },
  link:{
    color: theme.palette.primary.main,
    textDecoration: "underline"
  }
}));

function Signup(props) {
  useEffect(() => {
    AOS.init();
  }, []);

  const classes = useStyles();
  const classe = useStyle();

  return (
    <Paper className={classes.root}>
      <Grid
        container
        direction="row-reverse"
        alignItems="center"
        // spacing={3}
        // style={{ padding: "2rem" }}
      >
        <Grid item xs={12} md={6} style={{ padding: "2rem" }}>
          <div className={classes.header}>
            <span className={classes.formhead}>Sign Up :</span>
          </div>
          <PutInputs inputs={Step1} {...props} />
        </Grid>
        <Grid item xs={12} md={6} className={classe.col1} data-aos="fade-right">
          <div className={classe.divn}>
            <Typography variant="h5" align="left" className={classe.typo}>
              Get started with a free account
            </Typography>
            <Typography variant="caption" align="left" className={classe.typo2}>
              Elit esse exercitation consectetur aute.Sunt incididunt cillum
              quis esse in ut fugiat. Commodo mollit pariatur irure sit mollit
              amet Lorem velit deserunt ipsum quis quis. Enim amet occaecat
              mollit esse
            </Typography>
          </div>
          <div >
            <Typography variant="caption" align="left" className={classe.typo2} style={{paddingRight: 4}}>
              Already have a Startincub Account ?
            </Typography>
            <Link to='/signin' className={classe.link}>Login</Link>
          </div>
        </Grid>
      </Grid>
    </Paper>
  );
}

export default Signup;
