import { React, useEffect } from "react";
import formsvg from "../imgs/validation.webp";
import Grid from "@material-ui/core/Grid";
import "../../App.css";
import useStyles from "../styling/inputstyle";
import AOS from "aos";
import "aos/dist/aos.css";
import { Link, Typography } from "@material-ui/core";
import CircularProgress from "@material-ui/core/CircularProgress";
import { Box } from "@material-ui/core";
import { useHistory } from "react-router";

const values = [
  "FirstName",
  "LastName",
  "username",
  "password",
  "Rpassword",
  "email",
  "Sname",
  "sds",
  "website",
  "num",
  "dq1",
  "dq2",
  "dq3",
  "dq4",
  "dq5",
  "dq6",
  "dq7",
  "dq8",
  "dq9",
  "dq10",
  "dq11",
  "dq12",
  "dq13",
  "dq14",
  "dq15",
  "dq16",
  "dq17",
  "dq18",
  "dq19",
  "dq20",
  "dq21",
  "dq22",
  "dq23",
  "dq24",
];
const clearValues = (setValue) => {
  values.map((elem) => setValue({ target: { name: elem, value: "" } }));
};

function Submited(props) {
  const history = useHistory();
  useEffect(() => {
    AOS.init();
  }, []);

  const classes = useStyles();
  return props.loading ? (
    <Box align="center">
      <CircularProgress />
    </Box>
  ) : (
    <div>
      <Grid
        container
        alignItems="center"
        justifyContent="center"
        spacing={3}
        style={{ padding: "2rem" }}
      >
        <Grid item xs={12}>
          <Box display="flex" justifyContent="center" alignItems="center">
            <Typography
              variant="h2"
              color="error"
              style={{ fontFamily: "Montserrat, sans-serif" }}
              paragraph
            >
              Thank You !
            </Typography>
          </Box>

          <Box display="flex" justifyContent="center" alignItems="center">
            <img
              data-aos="fade-up"
              src={formsvg}
              alt="sing"
              style={{ width: "10rem" }}
              className={classes.ssvg}
            />
          </Box>
        </Grid>
        <Grid item xs={12} className={classes.centring}>
          <Box align="center">
            <div className={classes.submited}>
              <Typography
                variant="subtitle2"
                style={{ fontFamily: "Montserrat, sans-serif" }}
              >
                Thank you for filling out your information! We’ve sent you an
                email with your Verification LINK at the email address you
                provided. Please enjoy, and let us know if there’s anything else
                we can help you with. The StartIncub Team
              </Typography>
            </div>
            <Typography style={{ fontFamily: "Montserrat, sans-serif" }}>
              If you are not redirected automatically, please follow this:
            </Typography>
            <Link
              onClick={() => {
                clearValues(props.setValue);
                props.navigation.go(0);
                history.push("/");
              }}
              style={{ cursor: "pointer" }}
            >
              <Typography
                variant="subtitle2"
                style={{ fontFamily: "Montserrat, sans-serif" }}
                color="error"
              >
                Back to Home
              </Typography>
            </Link>
          </Box>
        </Grid>
      </Grid>
    </div>
  );
}

export default Submited;
