import { React, useEffect } from "react";
// import formsvg from "../imgs/startup.svg";
import Grid from "@material-ui/core/Grid";
import "../../App.css";
import PutInputs from "../component/Inputs";
import useStyles from "../styling/inputstyle";
import { Step2 } from "../inputdb/Step2";
import AOS from "aos"
import "aos/dist/aos.css";

function Dgeneral(props) {
  useEffect(() => {
    AOS.init();
  },[])
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <Grid
        container
        direction="row-reverse"
        alignItems="center"
        style={{ padding: "2rem" }}
        spacing={3}
      >
        <Grid item xs={12} >
          <div className={classes.header}>
            <span className={classes.formhead} >General Description :</span>
          </div>
          <PutInputs inputs={Step2} stype="inelab" {...props} />
        </Grid>
        {/* <Grid item xs={12} md={6} className={classes.bp}>
          <img data-aos="fade-right" src={formsvg} className={classes.ssvg} alt="" />
        </Grid> */}
      </Grid>
    </div>
  );
}

export default Dgeneral;
