
import { React } from "react";
import headStyle from "../../styling/HeaderStyle";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import useScrollTrigger from "@material-ui/core/useScrollTrigger";
import { useHistory, useLocation } from "react-router-dom";
import Button from "@material-ui/core/Button";
import MenuIcon from "@material-ui/icons/Menu";
import { Container } from "@material-ui/core";


export const HeaderHome = ({ handleDrawerOpen }) => {
  const options = { disableHysteresis: true, threshold: 0 };
  const classes = headStyle();
  const res = useScrollTrigger(options);
  const history = useHistory();
  let location = useLocation();
  const logo = (
    <svg
      width="54px"
      viewBox="0 0 142 138"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        fill={res !== true && location.pathname === "/" ? "white" : "#343f56"}
        d="M28.7273 26.0936L69.6364 2.60234L70.2424 2V23.6842L40.8485 40.5497C38.4505 41.868 37.7881 42.9578 37.5151 45.3684C37.4112 47.6951 37.4248 49.3422 37.5151 52.5965C37.6318 54.5284 37.9227 55.5979 39.9394 57.4152C41.7625 58.9478 43.1262 59.0658 46 58.3187L88.4242 33.6228V12.2398H89.0303L109.333 23.9854C113.636 26.6194 115.669 28.3206 116 30.3099V74.2807C115.524 77.9105 114.615 79.3965 112.364 81.5088L71.7576 105V83.3158L100.242 67.3538C104.258 64.4464 104.955 63.3353 104.485 55.9094C104.07 52.4125 103.45 51.1999 102.061 49.5848C100.085 48.6241 98.7454 48.4464 96 48.6813L52.9697 73.076V95.3626L29.3333 81.5088C27.7951 80.6727 27.0698 79.7349 26 77.2924V31.5146C26.28 29.3708 26.6781 28.177 28.7273 26.0936Z"
        stroke="white"
      />
      <path
        fill={res !== true && location.pathname === "/" ? "white" : "#343f56"}
        d="M0.858 131.81C4.87667 132.03 7.59733 132.14 9.02 132.14C9.35733 132.14 9.62133 132.045 9.812 131.854C10.0173 131.649 10.12 131.377 10.12 131.04V129.06H5.06C3.52 129.06 2.398 128.715 1.694 128.026C1.00467 127.322 0.66 126.2 0.66 124.66V124C0.66 122.46 1.00467 121.345 1.694 120.656C2.398 119.952 3.52 119.6 5.06 119.6H12.782V122.68H5.94C5.06 122.68 4.62 123.12 4.62 124V124.44C4.62 125.32 5.06 125.76 5.94 125.76H9.9C11.3373 125.76 12.3933 126.097 13.068 126.772C13.7427 127.447 14.08 128.503 14.08 129.94V131.04C14.08 132.477 13.7427 133.533 13.068 134.208C12.3933 134.883 11.3373 135.22 9.9 135.22C9.152 135.22 8.36 135.205 7.524 135.176L5.5 135.088C4.004 135.015 2.45667 134.912 0.858 134.78V131.81ZM22.4426 135H18.4826V122.68H13.8626V119.6H27.0626V122.68H22.4426V135ZM29.8083 135H25.5183L31.3483 119.6H35.7483L41.5783 135H37.2883L36.2983 132.25H30.7983L29.8083 135ZM31.7883 129.39H35.3083L33.5483 124.22L31.7883 129.39ZM42.6774 119.6H51.9174C53.4574 119.6 54.5721 119.952 55.2614 120.656C55.9654 121.345 56.3174 122.46 56.3174 124V125.98C56.3174 127.197 56.1121 128.143 55.7014 128.818C55.3054 129.493 54.6674 129.94 53.7874 130.16L56.5374 135H52.2474L49.7174 130.38H46.6374V135H42.6774V119.6ZM52.3574 124C52.3574 123.12 51.9174 122.68 51.0374 122.68H46.6374V127.3H51.0374C51.9174 127.3 52.3574 126.86 52.3574 125.98V124ZM65.9914 135H62.0314V122.68H57.4114V119.6H70.6114V122.68H65.9914V135ZM79.414 135H71.494V131.92H73.474V122.68H71.494V119.6H79.414V122.68H77.434V131.92H79.414V135ZM94.8123 135H90.6323L85.3523 125.76V135H81.3923V119.6H85.5723L90.8523 128.84V119.6H94.8123V135ZM109.343 134.78C106.072 135.073 103.359 135.22 101.203 135.22C99.7652 135.22 98.7092 134.883 98.0346 134.208C97.3599 133.533 97.0226 132.477 97.0226 131.04V124C97.0226 122.46 97.3672 121.345 98.0566 120.656C98.7606 119.952 99.8826 119.6 101.423 119.6H109.343V122.68H102.303C101.423 122.68 100.983 123.12 100.983 124V131.04C100.983 131.377 101.078 131.649 101.269 131.854C101.474 132.045 101.731 132.14 102.039 132.14C102.347 132.14 102.684 132.14 103.051 132.14C103.417 132.125 103.799 132.111 104.195 132.096C104.591 132.081 104.987 132.067 105.383 132.052C105.793 132.037 106.314 132.008 106.945 131.964C107.59 131.92 108.389 131.869 109.343 131.81V134.78ZM121.005 119.6H124.965V130.82C124.965 132.36 124.613 133.482 123.909 134.186C123.22 134.875 122.105 135.22 120.565 135.22H115.505C113.965 135.22 112.843 134.875 112.139 134.186C111.45 133.482 111.105 132.36 111.105 130.82V119.6H115.065V130.82C115.065 131.7 115.505 132.14 116.385 132.14H119.685C120.565 132.14 121.005 131.7 121.005 130.82V119.6ZM127.605 119.6H136.405C137.842 119.6 138.898 119.93 139.573 120.59C140.248 121.235 140.585 122.225 140.585 123.56C140.585 124.44 140.394 125.159 140.013 125.716C139.632 126.259 139.162 126.64 138.605 126.86C139.793 127.183 140.6 127.975 141.025 129.236C141.172 129.691 141.245 130.219 141.245 130.82C141.245 132.257 140.908 133.313 140.233 133.988C139.558 134.663 138.502 135 137.065 135H127.605V119.6ZM137.285 130.16C137.285 129.28 136.845 128.84 135.965 128.84H131.565V131.92H135.965C136.845 131.92 137.285 131.48 137.285 130.6V130.16ZM136.625 124C136.625 123.12 136.185 122.68 135.305 122.68H131.565V125.76H135.305C136.185 125.76 136.625 125.32 136.625 124.44V124Z"
      />
    </svg>
  );
  return (
    <Container maxWidth="xl" disableGutters={false}>
      <Toolbar>
        <IconButton
          size="medium"
          onClick={() => {
            history.push("/");
          }}
          edge="start"
          className={classes.icon}
        >
          {location.pathname !== "/" &&
          location.pathname !== "/signin" &&
          location.pathname !== "/Apply" &&
          location.pathname !== "/Contact"
            ? ""
            : logo}
        </IconButton>
        <div className={classes.grow} />
        <div className={classes.butitems}>
          <Button
            className={
              res !== true && location.pathname === "/"
                ? classes.applybutwhite
                : classes.applybut
            }
            onClick={() => {
              history.push("/signin");
            }}
          >
            Login
          </Button>
          {location.pathname === "/" ? (
            <Button
              variant="outlined"
              color={
                res !== true && location.pathname === "/"
                  ? "secondary"
                  : "primary"
              }
              className={
                res !== true && location.pathname === "/"
                  ? classes.applybutwhite2
                  : classes.applybut
              }
              onClick={() => {
                history.push("/Apply");
              }}
            >
              Apply
            </Button>
          ) : (
            <Button
              color="primary"
              className={classes.applybut}
              onClick={() => {
                history.push("/");
              }}
            >
              Home
            </Button>
          )}

          {/* <IconButton edge="end" className={classes.applybut} color="inherit">
              <img src={en} className="lang" lang="en" alt="" />
              <img src={fr} className="lang" lang="fr" alt="" />
            </IconButton> */}
        </div>
        <IconButton
          edge="end"
          className={classes.menubut}
          color="inherit"
          onClick={handleDrawerOpen}
        >
          {res !== true && location.pathname === "/" ? (
            <MenuIcon style={{ color: "white" }} />
          ) : (
            <MenuIcon />
          )}
        </IconButton>
      </Toolbar>
    </Container>
  );
};


