import React, { useState } from "react";
import {
  ListItem,
  ListItemIcon,
  ListItemText,
  List,
  Collapse,
} from "@material-ui/core";
// import ListItemIcon from "@material-ui/core/ListItemIcon";
// import ListItemText from "@material-ui/core/ListItemText";
import DeleteOutlineOutlinedIcon from "@material-ui/icons/DeleteOutlineOutlined";
import AddCircleOutlineOutlinedIcon from "@material-ui/icons/AddCircleOutlineOutlined";
import DashboardOutlinedIcon from "@material-ui/icons/DashboardOutlined";
import StarsOutlinedIcon from "@material-ui/icons/StarsOutlined";
import { Link } from "react-router-dom";
import PersonAddIcon from "@material-ui/icons/PersonAdd";
export const MainListItems = () => {
  const [open, setOpen] = useState(false);
  return (
    <div>
      <Link to="/Account/">
        <ListItem button>
          <ListItemIcon>
            <DashboardOutlinedIcon color="primary" />
          </ListItemIcon>
          <ListItemText primary="Dashboard" />
        </ListItem>
      </Link>
    

      <ListItem
        button
        onClick={() => {
          setOpen(!open);
        }}
      >
        <ListItemIcon>
          <PersonAddIcon color="primary" />
        </ListItemIcon>
        <ListItemText primary="Mentor Settings" />
      </ListItem>
      <Collapse in={open} timeout="auto" unmountOnExit>
        <List
          component="div"
          style={{ backgroundColor: "rgb(247, 249, 252)" }}
          disablePadding
        >
          <Link to="/Account/addMentor">
            <ListItem button>
              <ListItemIcon>
                <AddCircleOutlineOutlinedIcon />
              </ListItemIcon>
              <ListItemText primary="Add Mentor" />
            </ListItem>
          </Link>
          <Link to="/Account/deleteMentor">
            <ListItem button>
              <ListItemIcon>
                <DeleteOutlineOutlinedIcon />
              </ListItemIcon>
              <ListItemText primary="Delete Mentor" />
            </ListItem>
          </Link>
        </List>
      </Collapse>
      <Link to="/Account/startups">
        <ListItem button>
          <ListItemIcon>
            <StarsOutlinedIcon color="primary" />
          </ListItemIcon>
          <ListItemText primary="Startups" />
        </ListItem>
      </Link>
    </div>
  );
};
