import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import Divider from "@material-ui/core/Divider";
import Button from "@material-ui/core/Button";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import CardHeader from "@material-ui/core/CardHeader";
import { useEffect } from "react";
import { useHistory } from "react-router-dom";
import defaultlogo from "../../imgs/user.svg";
import Innercard from "./Innercard";
import { dbip } from "../../fetchingData/fatchdata";

const useStyles = makeStyles((theme) => ({
  icon: {
    marginRight: theme.spacing(2),
  },
  heroContent: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(8, 0, 6),
  },
  heroButtons: {
    marginTop: theme.spacing(4),
  },
  cardGrid: {
    paddingTop: theme.spacing(8),
    paddingBottom: theme.spacing(8),
  },
  card: {
    height: "100%",
    display: "flex",
    flexDirection: "column",
  },
  cardMedia: {
    paddingTop: "56.25%", // 16:9
  },
  cardContent: {
    flexGrow: 1,
  },
  footer: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(6),
  },
  but: {
    color: theme.palette.error.main,
    borderColor: theme.palette.error.main,
  },
  epitch: {
    fontFamily: "Montserrat, sans-serif",
    fontSize: "0.8rem",

    color: theme.palette.primary.main,
    [theme.breakpoints.up("sm")]: {
      fontSize: "1rem",
    },
  },
}));

export const StartupCard = (props) => {
  const history = useHistory();
  const role = props.data.role;
  const logo =
    props.startup.logo === "no-photo.jpg"
      ? undefined
      : `${dbip}/api/v1/startups/photo/` + props.startup.logo;
  const classes = useStyles();
  return (
    <Card className={classes.card}>
      <CardHeader
        // action={
        //   <IconButton aria-label="settings">
        //     <MoreVertIcon />
        //   </IconButton>
        // }
        title={props.startup.Sname}
        titleTypographyProps={{
          variant: "subtitle1",
          style: {
            fontFamily: "Montserrat, sans-serif",
            fontWeight: "bold",
            color: "#343f56",
          },
        }}
      />
      <Divider />
      <CardMedia
        className={classes.cardMedia}
        image={logo ? logo : defaultlogo}
        title="Image title"
      />
      <Divider />
      <CardContent className={classes.cardContent}>
        <Typography className={classes.epitch}>
          {props.startup.epitch}
        </Typography>
      </CardContent>
      <Innercard {...props} />
      <CardActions>
        {role === "admin" ? (  <Button
          size="small"
          variant="outlined"
          className={classes.but}
          onClick={() => {
            history.push({
              pathname: "/Account/Review",
              state: {
                id: props.startup._id,
              },
            });
          }}
        >
          Review
        </Button>) : (  <Button
          size="small"
          variant="outlined"
          className={classes.but}
          onClick={() => {
            history.push({
              pathname: "/Account/correction",
              state: {
                id: props.startup._id,
              },
            });
          }}
        >
          Evaluate
        </Button>)}
        {/* <Button
          size="small"
          variant="outlined"
          className={classes.but}
          onClick={() => {
            history.push({
              pathname: "/Account/correction",
              state: {
                id: props.startup._id,
              },
            });
          }}
        >
          Evaluate
        </Button> */}
      </CardActions>
    </Card>
  );
};

export const Getstartupcard = (props) => {
  useEffect(() => {
    const Getstartups = async () => {
      const data = await fetch(`${dbip}/api/v1/correction`, {
        method: "GET",
        headers: {
          "Content-type": "application/json",
          Authorization: `Bearer ${props.token}`,
        },
      });

      const res = await data.json();

      props.setStartup(res.data);
    };
    Getstartups();

    window.scrollTo(0, 0);
  }, []);
  const data = props.startup ? props.startup : [];
  return data.map((startup, index) => (
    <Grid item key={index} xs={12} md={6} lg={3}>
      <StartupCard startup={startup} key={index} data={props.data} />
    </Grid>
  ));
};
