import { Button, Container, Grid, Paper, Typography } from "@material-ui/core";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import rocket from "../../imgs/R.png";
import { useEffect } from "react";
import "aos/dist/aos.css";
import AOS from "aos";
import { Link } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  paper: {
    borderRadius: "16px",
    backgroundImage: `linear-gradient( 135deg , ${theme.palette.error.main} 0%, ${theme.palette.error.dark}  100%)`,
    maxWidth: "456px",
    margin: "auto",
    [theme.breakpoints.up("md")]: {
      maxWidth: "100%",
    },
  },
  typo: {
    margin: "0px",
    fontFamily: "Montserrat, sans-serif",
    fontWeight: 700,
    fontSize: "2rem",
    lineHeight: 1.33333,
    textAlign: "center",
    color: "white",
    [theme.breakpoints.only("md")]: {
      fontSize: "2.5rem",
      textAlign: "left",
    },
    [theme.breakpoints.up("lg")]: {
      fontSize: "2.75rem",
      textAlign: "left",
    },

    paddingBottom: theme.spacing(6),
  },
  container: {
    display: "flex",
    alignItems: "center",
    [theme.breakpoints.up("md")]: {
      alignItems: "self-start",
      paddingLeft: theme.spacing(10),
    },
    flexDirection: "column",
    justifyContent: "center",
    paddingBottom: "2rem",
  },
  rocket: {
    maxWidth: "460px",
    width: "100%",
  },
}));

const ColorButton = withStyles((theme) => ({
  root: {
    display: "inline-flex",
    alignItems: "center",
    justifyContent: "center",
    position: "relative",
    boxSizing: "border-box",
    WebkitTapHighlightColor: "transparent",
    outline: "0px",
    border: "0px",
    margin: "0px",
    cursor: "pointer",
    userSelect: "none",
    verticalAlign: "middle",
    appearance: "none",
    textDecoration: "none",
    fontFamily: '"Public Sans", sans-serif',
    fontWeight: "700",
    fontSize: "0.9375rem",
    lineHeight: "1.71429",
    textTransform: "capitalize",
    minWidth: "64px",
    padding: "8px 22px",
    borderRadius: "8px",
    height: "48px",
    whiteSpace: "nowrap",
    boxShadow: "rgb(145 158 171 / 24%) 0px 8px 16px 0px",
    color: "rgba(0, 0, 0, 0.87)",
    backgroundColor: "rgb(255, 255, 255)",
  },
}))(Button);

export const Getstarted = () => {
  const classes = useStyles();
  useEffect(() => {
    AOS.init({ once: false });
  }, []);
  return (
    <Container
      maxWidth="lg"
      style={{
        paddingBottom: "2rem",
      }}
    >
      <Paper elevation={3} className={classes.paper}>
        <Grid container justifyContent="flex-start" align="center">
          <Grid container item xs={12} md={5} justifyContent="flex-start" align="center" id="rocket">
            <img
              src={rocket}
              className={classes.rocket}
              data-aos="fade-right"
              alt=""
              id="rocket"
            />
          </Grid>
          <Grid container item xs={12} md={5}>
            <Container maxWidth="sm" className={classes.container}>
              <Typography
                variant="h2"
                className={classes.typo}
                data-aos="fade-down"
              >
                Get started with Startincub today
              </Typography>
              <Link to="/Apply">
              <ColorButton variant="contained" data-aos="fade-up">
                Apply now
              </ColorButton>
              </Link>
            </Container>
          </Grid>
        </Grid>
      </Paper>
    </Container>
  );
};
