import Chart, { Piechart } from "./Chart";
import Deposits from "./Deposits";
import Orders from "./Orders";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import clsx from "clsx";
import { useStyles } from "../../styling/dashbordstyle";
import { useEffect } from "react";
import { Box } from "@material-ui/core";

const griditems = [
  {
    title: "Recomanded",
  },
  {
    title: "Strongly recomanded",
  },
  {
    title: "Not recemanded",
  },
  {
    title: "Startup Total",
  },
];

export const DashOverView = ({ startup , data}) => {
  console.log(startup);
  const role = data.role;

  const griditems =
    role === "admin"
      ? [
        {
          title: "Strongly recomanded",
          style: "rgb(200, 250, 205)"
        },
          {
            title: "Recomanded",
            style: "rgb(208, 242, 255)"
          },
          {
            title: "Not recemanded",
            style: "rgb(255, 231, 217)"
          },
          {
            title: "Startup Total",
            style: "rgb(255, 247, 205)"
          },
        ]
      : [
          {
            title: "onProgress",
          },
          {
            title: "Evaluated",
          },
          {
            title: "Tasks Total",
          },
        ];
  const classes = useStyles();
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);
  return (
    <Grid container spacing={3}>
      {griditems.map((elem, index) => (
        <Grid item xs={12} md={6} lg={3} key={index}>
          <Paper className={fixedHeightPaper} style={{backgroundColor:elem.style}}>
            <Deposits startup={startup} griditem={elem}  />
          </Paper>
        </Grid>
      ))}
      <Grid item xs={12} md={7}>
        <Paper className={classes.paper} style={{height:240}}>
          <Chart startup={startup} role={role}/>
        </Paper>
      </Grid>
      <Grid item xs={12} md={5}>
        <Paper className={classes.paper} style={{height:240}}>
          <Piechart startup={startup} role={role}/>
        </Paper>
      </Grid>
      <Grid item xs={12}>
        <Paper className={classes.paper}>
          <Orders startup={startup} />
        </Paper>
      </Grid>
    </Grid>
  );
};
