import {Typography, Box} from "@material-ui/core";
import LinearProgress from "@material-ui/core/LinearProgress";
import { withStyles } from "@material-ui/core/styles";


export default function Innercard(props) {
  console.log(props.startup)
  const column = Object.keys(props.startup.mentor)[0];
  const pvalue = props.startup.mentor[column].total === -1 ? 0 : props.startup.mentor[column].total
  const progvalue = Math.floor((pvalue / 24)*100);
  const BorderLinearProgress = withStyles((theme) => ({
      root: {
        height: 10,
        borderRadius: 5,
      },
      colorPrimary: {
        backgroundColor:
          theme.palette.grey[theme.palette.type === "light" ? 200 : 700],
      },
      bar: {
        borderRadius: 5,
        backgroundColor: progvalue === 100 ?theme.palette.success.light : theme.palette.error.main,
      },
    }))(LinearProgress);
  return (
    <>
      <div style={{ paddingLeft: "4px" }}>
        <Typography variant="caption" color="primary" >Startup’s development stage :</Typography>
        <Typography variant="caption" color="error" paragraph>
          {props.startup.sds}
        </Typography>
      </div>
      <div style={{ paddingLeft: "4px" , paddingRight:"4px"}}>
        <Typography variant="caption" color="primary">Review Progress</Typography>
        <Box display="flex" alignItems="center">
          <Box width="100%" mr={1}>
            <BorderLinearProgress variant="determinate" value={progvalue} />
          </Box>
          <Box minWidth={35}>
            <Typography variant="body2" color="textSecondary">
              {progvalue}%
            </Typography>
          </Box>
        </Box>
      </div>
    </>
  );
}
