import Button from "@material-ui/core/Button";
import { useHistory } from "react-router-dom";
import Icon from "@material-ui/core/Icon";
import { CircularProgress, Box } from "@material-ui/core";

var uniqid = require("uniqid");

function Butns(props) {
  const history = useHistory();
  if (props.login)
    return (
      <Box
        position="relative"
        display="flex"
        justifyContent="center"
        style={{ width: "50%" }}
      >
        <Button
          variant="outlined"
          color="primary"
          disabled={props.Loading}
          style={{ width: "100%" }}
          onClick={() => {
            props.setLoading(true);
            props.auth();
            // props.setLoading(false);
          }}
        >
          {props.ftype}
        </Button>
        <div
          style={{
            paddingTop: "4px",
            position: "absolute",
            top: "50%",
            right: "50%",
            transform: "translate(50%,-50%)",
          }}
        >
          {props.Loading ? (
            <CircularProgress
              size={24}
              style={{
                // top: "6px",
                width: "24px",
                height: "24px",
                // position: "absolute",
                zIndex: 1,
                // left: "44%",
              }}
            />
          ) : (
            ""
          )}
        </div>
      </Box>
    );
  if (props.end === "end") {
    return (
      <div style={{ display: "flex", justifyContent: "flex-end" }}>
        <Button
          variant="outlined"
          color="primary"
          style={{ width: "10%" }}
          onClick={props.handlback}
          key={uniqid()}
        >
          Back
        </Button>
        <Button
          variant="contained"
          color="primary"
          style={{ width: "10%", marginLeft: "10px" }}
          onClick={props.addStartup}
          key={uniqid()}
        >
          Submit
        </Button>
      </div>
    );
  } else if (props.Btype === "Double") {
    return (
      <div style={{ display: "flex", justifyContent: "flex-end" }}>
        <Button
          variant="outlined"
          color="primary"
          style={{ width: "10%" }}
          onClick={props.handlback}
          key={uniqid()}
        >
          Back
        </Button>
        <Button
          variant="contained"
          color="primary"
          style={{ width: "10%", marginLeft: "10px" }}
          onClick={props.handlNext}
          key={uniqid()}
        >
          Next
        </Button>
      </div>
    );
  } else if (props.Btype === "Single") {
    return (
      <Button
        variant="outlined"
        key={uniqid()}
        color="primary"
        style={{ width: "50%" }}
        onClick={props.handlNext}
      >
        Next
      </Button>
    );
  }
  if (props.ftype === "cos")
    return (
      <Button
        variant="outlined"
        color="primary"
        style={{ width: "50%" }}
        onClick={() => {
          props.func();
        }}
      >
        {props.btnname}
      </Button>
    );
  if (props.ftype)
    return (
      <Button
        variant="outlined"
        color="primary"
        style={{ width: "50%" }}
        onClick={() => {
          history.push(`/${props.where}`);
        }}
      >
        {props.ftype}
      </Button>
    );
    
}
export default Butns;
