import { React } from "react";
import Grid from "@material-ui/core/Grid";
import "../../App.css";
import PutInputs from "../component/Inputs";
import useStyles from "../styling/inputstyle";
import { Step4 } from "../inputdb/Step4";

function Findustry(props) {
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <Grid
        container
        direction="row-reverse"
        alignItems="center"
        spacing={3}
        style={{ padding: "2rem" }}
      >
        <Grid item xs={12}>
          <div className={classes.header}>
            <span className={classes.formhead}>Financials / Business model :</span>
          </div>
          <PutInputs inputs={Step4} stype="inelab" {...props} />
        </Grid>
      </Grid>
    </div>
  );
}

export default Findustry;
