import React from 'react';
import { Link } from "react-router-dom";
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Title from './Title';
import { Typography } from '@material-ui/core';

// Generate Order Data

// function preventDefault(event) {
//   event.preventDefault();
// }

const useStyles = makeStyles((theme) => ({
  seeMore: {
    marginTop: theme.spacing(3),
  },
}));

export default function Orders({startup}) {
  const classes = useStyles();
  return (
    <React.Fragment>
      <Title>Recent Startups</Title>
      <Table size="small">
        <TableHead>
          <TableRow>
            <TableCell>Name</TableCell>
            <TableCell>Devlompent Stage</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {startup?.slice(0).reverse().map((row, index) => (
            <TableRow key={index}>
              <TableCell>{row.Sname}</TableCell>
              <TableCell>{row.sds}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      <div className={classes.seeMore}>
        <Link color="primary" to="/Account/startups">
          <Typography color="primary" style={{textDecoration: "underline"}}>
              See more Startups
          </Typography>
        </Link>
      </div>
    </React.Fragment>
  );
}