import { useState, useEffect } from "react";

export default function useToken() {
  const getToken = () => {
    const tokenString = localStorage.getItem("token");
    const userToken = tokenString;
    return userToken !== "undefined" ? userToken : "";
  };

  const [token, setToken] = useState(getToken());
  useEffect(() => {
    setToken(getToken());
  }, [setToken, getToken]);
  const saveToken = (userToken) => {
    if (userToken) {
      localStorage.setItem("token",userToken);
      setToken(userToken.token);
    }
  };

  return {
    GetToken: saveToken,
    token,
  };
}

export const useCorrection = () => {
  const defaultValues = [
    { cq1: -1 },
    { cq2: -1 },
    { cq3: -1 },
    { cq4: -1 },
    { cq5: -1 },
    { cq6: -1 },
    { cq7: -1 },
    { cq8: -1 },
    { cq9: -1 },
    { cq10: -1 },
    { cq11: -1 },
    { cq12: -1 },
    { cq13: -1 },
    { cq14: -1 },
    { cq15: -1 },
    { cq16: -1 },
    { cq17: -1 },
    { cq18: -1 },
    { cq19: -1 },
    { cq20: -1 },
    { cq21: -1 },
    { cq22: -1 },
    { cq23: -1 },
    { cq24: -1 },
  ];


  const [cqs, setCorr] = useState(defaultValues);

  const putCorr = (data) => {
    setCorr(data);
  };

  const saveCorr = (indata, name) => {
    const nar = cqs.map((elem) => {
      if (Object.keys(elem)[0] === name) elem[name] = indata;
      return elem;
    });
    setCorr(nar);
  };
  return {
    putCorr,
    setCorrection: saveCorr,
    cqs,
  };
};

export function useFbSr() {

  const [fb, setFb] = useState("");
  const [sr, setSr] = useState("");


  const putFbSr = (fb, sr) => {
   
    setFb(fb? fb : "");
    setSr(sr ? sr : "");
  };



  const saveFbSr = (event) => {
    // console.log(event);
    if (event.target.name === "fb") {
      setFb(event.target.value);
    } else {
      setSr(event.target.value);
    }
  };

  return {
    GetFbSr: saveFbSr,
    fb,
    sr,
    putFbSr
  };
}
